<template>
  <transition name="loading-fade">
    <div class="v-loading-mask" v-show="show">
      <div class="v-loading" :class="className">
        <span class="v-loading-dot" v-show="!tip.length"></span>
        <span class="v-loading-txt" v-html="tip"></span>
      </div>
    </div>
  </transition>
</template>
<script>
  const oSizeClass = {
    'default': '',
    'big': 'v-loading-big',
    'small': 'v-loading-small'
  };
  import * as Util from "../../utils";
  const prefix = 'v-loading';
  export default{
    name: prefix,
    props: {
      show: {
        type: Boolean,
        coerce: Util.coerceBoolean,
        default: false
      },
      size: {
        type: String,
        default: 'default'
      },
      tip: {
        type: String,
        default: ''
      }
    },
    computed: {
      className () {
        return oSizeClass[this.size];
      }
    }
  }
</script>



