<template>
  <div class="v-upload-drag" :class="multiple ? 'multiple': ''"
    @click.stop="handleClick"
    @drop.prevent="handleDrop"
    @dragover.prevent="dragOver = true"
    @dragleave.prevent="dragOver = false"
  >
    <input type="file" ref="file" @click.stop @change="handleChange" :multiple="multiple" :accept="computed_accept" :disabled="disabled" style="display:none;">
    <div class="v-upload-drag-container" v-show="showGuide">
      <p class="v-upload-drag-container-icon">
        <v-icon type="cloud-upload"/>
      </p>
      <p class="v-upload-drag-container-text"></p>
      <p class="v-upload-hint"><slot></slot></p>
    </div>
    <transition-group tag="div" class="v-upload-drag-list" name="upload-zoom" @before-enter="transition" @after-leave="transition">
      <div @click.stop class="v-upload-drag-preview" v-for="item in computed_fileList" :class="item.status" :key="item.uid">
        <div class="v-upload-drag-preview-content">
          <img v-if="checkImage(item.type)" :src="item.url" />
          <div v-else class="v-upload-drag-preview-content-icon">
            <v-icon v-if="item.status=='uploading'"  type="cloud-upload"/>
            <v-icon v-if="item.status=='error'"  type="cloud-upload"/>
            <v-icon v-if="item.status=='done'"  type="file-text"/>
          </div>
          <p v-if="!checkImage(item.type) && item.status !='uploading'" class="v-upload-drag-preview-content-title">{{item.name}}</p>
        </div>
        <div class="v-upload-drag-preview-mask">
          <div class="v-upload-drag-preview-mask-btns">
            <span v-if="!multiple" @click.stop="handleClick"><v-icon type="upload"/><span></span></span>
            <span @click.stop="onRemove(item)"><v-icon type="trash"/><span></span></span>
          </div>
        </div>
        <div class="v-upload-drag-preview-progress">
          <v-progress
            :show-info="showInfo"
            :percent="item.percent"
            :stroke-width="strokeWidth"
          ></v-progress>
        </div>
        <div class="v-upload-drag-preview-error" v-if="item.status=='error'">
          <v-icon type="times"/>
        </div>
        <div class="v-upload-drag-preview-success" v-if="item.status=='done'">
          <v-icon type="check"/>
        </div>
        <div class="v-upload-drag-preview-title">{{item.name}}</div>
      </div>
    </transition-group>
  </div>
</template>
<script type="text/javascript">
  const prefix = 'v-upload-drag';
  import uploader from './uploader';
  export default {
    name: prefix,
    mixins: [ uploader ],
    data () {
      return {
        showInfo: false,
        strokeWidth: 8,
        showGuide: true,
        dragOver: false,
      };
    },
    methods: {
      transition () {
        if(!this.computed_fileList.length) {
          this.showGuide = true;
        } else{
          this.showGuide = false;
        }
      },
    }
  }
</script>


