import Vue from 'vue';
import Notification from './Notification.vue';
import * as Util from "../../../utils";

Notification.newInstance = properties => {
  const _props = properties || {};

  let props = '';
  Object.keys(_props).forEach(prop => {
    props += ' :' + Util.camelcaseToHyphen(prop) + '=' + prop;
  });

  var Profile = Vue.extend({
    template: `<notification${props}></notification>`,
    components: { Notification },
    data () {
      return _props;
    },
  })


  //  Profile 
  var notices = new Profile().$mount();
  document.body.appendChild(notices.$el);

  return {
    component: notices,
    notice (noticeProps) {
      notices.$children[0].add(noticeProps);
    },
    remove (key) {
      notices.$children[0].remove(key);
    },
    destroy () {
      document.body.removeChild(notices.$el);
    }
  }
};

export default Notification;



