import './style/index.scss';

import vAffix from './lib/affix';
import vIcon from './lib/icon';
import vButton from './lib/button';
import { vCheckbox, vCheckboxGroup } from './lib/checkbox';
import { vRadio, vRadioGroup } from './lib/radio';
import vSwitch from './lib/switch';
import vDatePicker from './lib/datePicker';
import { vUpload, vUploadDrag }  from './lib/upload';
import vMultiselect from './lib/multiselect';
import vInput from './lib/input';
import { vRow, vCol }  from './lib/layout';
import { vForm , vFormItem, vSearchForm } from './lib/form';
import vMessage from './lib/message';
import vModal from './lib/modal';
import vNotify from './lib/notify';
import vTooltip from './lib/tooltip';
import vPopover from './lib/popover';
import vPopconfirm from './lib/popconfirm';
import vTable from './lib/table';
import vPagination from './lib/pagination';
import vProgress from './lib/progress';
import vLoading from './lib/loading';
import vAnimate from './lib/animate';
import { vTabs, vTabItem }  from './lib/tab';
import vSearch from './lib/search';
import vTree from './lib/tree';
import vCascader from './lib/cascader';

const install = function(Vue, opts = {}) {
  /* istanbul ignore if */
  if (install.installed) return;
  
  Vue.component(vAffix.name, vAffix);
  Vue.component(vIcon.name, vIcon);
  Vue.component(vButton.name, vButton);
  Vue.component(vCheckbox.name, vCheckbox);
  Vue.component(vCheckboxGroup.name, vCheckboxGroup);
  Vue.component(vRadio.name, vRadio);
  Vue.component(vRadioGroup.name, vRadioGroup);
  Vue.component(vSwitch.name, vSwitch);
  Vue.component(vDatePicker.name, vDatePicker);
  Vue.component(vUpload.name, vUpload);
  Vue.component(vUploadDrag.name, vUploadDrag);
  Vue.component(vMultiselect.name, vMultiselect);
  Vue.component(vCol.name, vCol);
  Vue.component(vRow.name, vRow);
  Vue.component(vInput.name, vInput);
  Vue.component(vFormItem.name, vFormItem);
  Vue.component(vForm.name, vForm);
  Vue.component(vSearchForm.name, vSearchForm);
  Vue.component(vModal.name, vModal);
  Vue.component(vMessage.name, vMessage);
  Vue.component(vNotify.name, vNotify);
  Vue.component(vPopover.name, vPopover);
  Vue.component(vTooltip.name, vTooltip);
  Vue.component(vPopover.name, vPopover);
  Vue.component(vPopconfirm.name, vPopconfirm);
  Vue.component(vProgress.name, vProgress);
  Vue.component(vLoading.name, vLoading);
  Vue.component(vTable.name, vTable);
  Vue.component(vPagination.name, vPagination);
  Vue.component(vAnimate.name, vAnimate);
  Vue.component(vTabs.name, vTabs);
  Vue.component(vTabItem.name, vTabItem);
  Vue.component(vSearch.name, vSearch);
  Vue.component(vTree.name, vTree);
  Vue.component(vCascader.name, vCascader);
}

/* istanbul ignore if */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
};

module.exports = {
  version: '2.0.0',
  install,
  vAffix,
  vIcon,
  vButton,
  vCheckbox,
  vCheckboxGroup,
  vRadio, 
  vRadioGroup,
  vSwitch,
  vDatePicker,
  vUpload,
  vUploadDrag,
  vMultiselect,
  vInput,
  vRow, 
  vCol,
  vFormItem,
  vForm,
  vSearchForm,
  vModal,
  vMessage,
  vNotify,
  vPopover,
  vTooltip,
  vPopconfirm,
  vProgress,
  vLoading,
  vTable,
  vPagination,
  vAnimate,
  vTabs,
  vTabItem,
  vSearch,
  vTree,
  vCascader
};



