/*
  turn boolean
*/
export function coerceBoolean(val) {
  if(typeof val !== "string") {
    return val;
  } else if(val === "true") {
    return true;
  } else if(val === "false") {
    return false;
  } else if(val === "null") {
    return false;
  } else if(val === "undefined") {
    return false;
  } 
}

/*
 turn number
*/
export function coerceNumber(val) {
  if (typeof val !== 'string') {
    return val;
  } else {
    var parsed = Number(val);
    return isNaN(parsed) ? val : parsed;
  }
}

/*
  turn string
*/
export function coerceToString (value) {
  return value == null
    ? ''
    : value.toString();
}

/*
  
  
  is_globaltrue
*/
export function trim (str, is_global) {
  str = coerceToString(str);
  var result;
  result = str.replace(/(^\s+)|(\s+$)/g,"");
  if(is_global) {
    result = result.replace(/\s/g,"");
  }
  return result;
}

/*
  
*/
export function getLen (obj) {
  var size = 0;
  if(isPlainObject(obj)) {
    for (var key in obj) {
      if (obj.hasOwnProperty(key)) size++;
    }
    return size;
  } else if(isArray(obj)) {
    return obj.length;
  } else if(isString(obj) || isNumber(obj)) {
    obj = trim(obj);
    return obj.length;
  } else{
    console.log('error get len');
    return undefined;
  }
}

/*
  check object
*/
export function isObject (obj) {
  return obj !== null && typeof obj === 'object';
}
export function isPlainObject (obj) {
  return Object.prototype.toString.apply(obj) == '[object Object]';
}

/*
  check array
*/
export function isArray (arr) {
  return Array.isArray(arr);
}

/*
  check function
*/
export function isBoolean (fun) {
  return Object.prototype.toString.apply(fun) == '[object Boolean]';
}

/*
  check function
*/
export function isString (fun) {
  return Object.prototype.toString.apply(fun) == '[object String]';
}

/*
  check function
*/
export function isNumber (fun) {
  return Object.prototype.toString.apply(fun) == '[object Number]';
}

/*
  check function
*/
export function isFunction (fun) {
  return Object.prototype.toString.apply(fun) == '[object Function]';
}

/*
  Check whether the object has the property
*/
export function hasOwn (obj, key) {
  return Object.prototype.hasOwnProperty.call(obj, key);
}

/*
  Define a property
*/
export function def (obj, key, val, enumerable) {
  Object.defineProperty(obj, key, {
    value: val,
    enumerable: !!enumerable,
    writable: true,
    configurable: true
  })
}

/*
  get new array
*/
export function toArray(list, start) {
  start = start || 0;
  var i = list.length - start;
  var ret = new Array(i);
  while (i--) {
    ret[i] = list[i + start];
  }
  return ret;
}

/*
  extend and copy obj
  extend(dest,src1,src2,src3...); src1,src2,src3...dest,dest
  extend(boolean,dest,src1,src2,src3...) 
*/
export function extend() {
  var src, copyIsArray, copy, name, options, clone,
    target = arguments[0] || {},
    i = 1,
    length = arguments.length,
    deep = false;

  // Handle a deep copy situation
  if ( typeof target === "boolean" ) {
    deep = target;

    // skip the boolean and the target
    target = arguments[ i ] || {};
    i++;
  }

  // Handle case when target is a string or something (possible in deep copy)
  if ( typeof target !== "object" ) {
    target = {};
  }

  // extend jQuery itself if only one argument is passed
  if ( i === length ) {
    target = this;
    i--;
  }

  for ( ; i < length; i++ ) {
    // Only deal with non-null/undefined values
    if ( (options = arguments[ i ]) != null ) {
      // Extend the base object
      for ( name in options ) {
        src = target[ name ];
        copy = options[ name ];

        // Prevent never-ending loop
        if ( target === copy ) {
          continue;
        }

        // Recurse if we're merging plain objects or arrays
        if ( deep && copy && (isPlainObject(copy) || (copyIsArray = isArray(copy)) ) ) {
          if ( copyIsArray ) {
            copyIsArray = false;
            clone = src && isArray(src) ? src : [];

          } else {
            clone = src && isPlainObject(src) ? src : {};
          }

          // Never move original objects, clone them
          target[ name ] = extend( deep, clone, copy );

        // Don't bring in undefined values
        } else if ( copy !== undefined ) {
          target[ name ] = copy;
        }
      }
    }
  }

  // Return the modified object
  return target;
}

/*
  
*/
export function throttle (func, wait, options) {
  let context, args, result
  let timeout = null
  let previous = 0
  if (!options) options = {}
  var later = function () {
    previous = options.leading === false ? 0 : Date.now()
    timeout = null
    result = func.apply(context, args)
    if (!timeout) context = args = null
  }
  return function () {
    var now = Date.now()
    if (!previous && options.leading === false) previous = now
    var remaining = wait - (now - previous)
    context = this
    args = arguments
    if (remaining <= 0 || remaining > wait) {
      if (timeout) {
        clearTimeout(timeout)
        timeout = null
      }
      previous = now
      result = func.apply(context, args)
      if (!timeout) context = args = null
    } else if (!timeout && options.trailing !== false) {
      timeout = setTimeout(later, remaining)
    }
    return result
  }
}

/*
  
  equal objA objB
*/
export function shallowequal (objA, objB) {
  if (objA === objB) return true;
  if (typeof objA !== 'object' || objA === null || typeof objB !== 'object' || objB === null) return false;
  const aKeys = Object.getOwnPropertyNames(objA);
  const aLen = aKeys.length;
  if(aLen != Object.getOwnPropertyNames(objB).length) return false;
  const bHasOwnProperty = Object.prototype.hasOwnProperty.bind(objB);
  let key;
  for (let i = 0; i < aLen; i++) {
    key = aKeys[i];
    if (!bHasOwnProperty(key)) return false;
    const valueA = objA[key];
    const valueB = objB[key];
    if(valueA !== valueB) return false;
  }
  return true;
}

/*
 onOk->on-ok
*/
export function camelcaseToHyphen (str) {
  return str.replace(/([a-z])([A-Z])/g, '$1-$2').toLowerCase();
}



// 
Date.prototype.Format = function(format) { //author: meizz
  var o = {
      "Y+": this.getFullYear(), // 
      "M+": this.getMonth() + 1, //
      "d+": this.getDate(), // 
      "h+": this.getHours(), //
      "m+": this.getMinutes(), //
      "s+": this.getSeconds(), //
      "q+": Math.floor((this.getMonth() + 3) / 3), //
      "S": this.getMilliseconds() //
  };
  if(/(Y+)/.test(format)) {  
    format = format.replace(RegExp.$1, this.getFullYear());  
  }  
    
  for(var k in o) {  
    if(new RegExp("("+ k +")").test(format)) {  
    format = format.replace(RegExp.$1, RegExp.$1.length==1 ? o[k] : ("00"+ o[k]).substr((""+ o[k]).length));  
    }  
  } 
  return format;
}
function getRelativeDate(timestampstr) {
  var timestamp = parseInt(timestampstr);
  timestamp = isNaN(timestamp) ? 0 : timestamp;
  var thenT = new Date(timestamp);
  var thenY = thenT.getFullYear();
  thenT.setHours(0);
  thenT.setMinutes(0);
  thenT.setSeconds(0);
  var nowtime = new Date();
  var nowtimeY = nowtime.getFullYear();
  nowtime.setHours(0);
  nowtime.setMinutes(0);
  nowtime.setSeconds(0);
  var delt = Math.round((nowtime.getTime() - thenT.getTime()) / 1000 / 86400);
  var day_def = {
    '0': '',
    '1': '',
    '2': ''
  };
  if(delt == 0) {
    return new Date(timestamp).Format('hh:mm');
  } else if(delt > 0 && delt <= 2){
    return day_def[delt] + ' ' + (new Date(timestamp).Format('hh:mm'));
  } else if(thenY == nowtimeY) {
    return new Date(timestamp).Format('MMdd hh:mm');
  } else{
    return new Date(timestamp).Format('YYMMdd');
  }
}

// 
function browserName() {
  window.oBrowser = {};   
  if(navigator.userAgent.indexOf("MSIE") > 0) {  
    oBrowser.name = 'MSIE';
    oBrowser.ie = true;  
  } else if(navigator.userAgent.indexOf("Firefox") > 0){ 
    oBrowser.name = 'Firefox'; 
    oBrowser.firefox = true;  
  } else if(navigator.userAgent.indexOf("Chrome") > 0) {
    oBrowser.name = 'Chrome'; 
    oBrowser.chrome = true;  
  } else if(navigator.userAgent.indexOf("Safari") > 0) {
    oBrowser.name = 'Safari';
    oBrowser.safari = true;
  } else if(navigator.userAgent.indexOf("Opera") >= 0) {
    oBrowser.name = 'Opera';
    oBrowser.opera = true;
  } else {
    oBrowser.name = 'unknow';
  }  
}

// (bottom)
function getElementPos($obj) {
  var box = $obj.getBoundingClientRect();
  var body = document.body;
  var pageX = (box.left + box.right) / 2 + (body.scrollLeft || 0)  - ( body.clientLeft || 0);
  var pageY = box.bottom + (body.scrollTop  || 0) - (body.clientTop  || 0);
  var pos = {
    top: pageY,
    left: pageX
  };
  return pos;
}

// 
function getTargetPos ($obj) {
  const pos = $obj.getBoundingClientRect();
  const _pos = {
    left: pos.left,
    top: pos.top,
    right: pos.right,
    bottom: pos.bottom,
    width: pos.width,
    height: pos.height
  }
  return _pos;
}



