import align from 'dom-align';
export default {
  methods: {
    doAlign () {
      const trigger = this.$slots.trigger[0].elm;
      const popupNode = this.$refs.popup;
      if (!trigger || !popupNode) return;
      this.alignCallback(popupNode, align(popupNode, trigger, this.align));
    },
    bindPopupEvent () {
      const popupNode = this.$refs.popup;
      if (popupNode) {
        popupNode.addEventListener('mouseenter', this.onMouseEnter.bind(this), false);
        popupNode.addEventListener('mouseleave', this.onMouseLeave.bind(this), false);
      }
    },
    offPopupEvent () {
      const popupNode = this.$refs.popup;
      if (popupNode) {
        popupNode.removeEventListener('mouseenter', this.onMouseEnter.bind(this), false);
        popupNode.removeEventListener('mouseleave', this.onMouseLeave.bind(this), false);
      }
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.bindPopupEvent();
    });
  },
  beforeDestroy () {
    this.offPopupEvent();
    const popupNode = this.$refs.popup;
    popupNode &&
    popupNode.parentNode === document.body &&
    document.body.removeChild(popupNode);
  }
}


