<template>
  <div :class="classes">
    <slot></slot>
  </div>
</template>
<script>
  export default{
    name: 'v-button-group',
    props: {
      direction: {
        type: String,
        default: 'horizontal'
      },
      shape: {
        type: String,
        default: 'default'
      },
    },
    computed: {
      classes () {
        return [
          {
            ['btn-group']: this.direction == 'horizontal' || this.direction == ''
          },
          {
            ['btn-group-vertical']: this.direction == 'vertical'
          }
        ];
      }
    }
  }
</script>


