<template>
  <span>
    <slot name="trigger"></slot>
    <!-- <transition :name="transition"> -->
      <div ref="popup" :class="popupCls" v-show="value"
        @click.stop>
        <div class="tooltip-arrow"></div>
        <div class="tooltip-inner" v-html="title"></div>
      </div>
    <!-- </transition> -->
  </span>
</template>
<script>
  import trigger from "../base/trigger/trigger";
  const prefix = 'v-tooltip';
  export default{
    name: prefix,
    mixins: [ trigger ],
    props: {
      trigger: {
        type: String,
        default: 'hover'
      },
      title: {
        type: String,
        default: ''
      },
      transition: {
        type: String,
        default: 'ease'
      },
    },
    data () {
      return {
        prefixCls: prefix
      }
    }
  }
</script>


