<template>
  <div :class="wrapCls">
    <v-input ref="reservationtime" v-model.lazy="_value" :placeholder="placeholder" icon="calendar"></v-input>
  </div>
</template>
<script>
  const prefix = 'v-datepicker';
  import * as Util from "../../utils";
  import $ from 'jquery';
  import moment from 'moment';
  import vInput from "../input";
  export default{
    name: prefix,
    components: { vInput },
    props: {
      value: {},
      ranges: {
        type: Array,
        default () {
          return [];
        }
      },
      opens: {
        type: String,
        default: 'left'
      },
      timePicker: {
        type: Boolean,
        default: false 
      },
      showDropdowns: {
        type: Boolean,
        default: false 
      },
      format: {
        type: String,
        default: 'YYYY-MM-DD'
      },
      singleDatePicker: {
        type: Boolean,
        default: false 
      },
      minDate: {
        type: String,
        default: ''
      },
      maxDate: {
        type: String,
        default: ''
      },
      placeholder: {
        type: String,
        default: '   ~  '
      }
    },
    data () {
      return {
        wrapCls: prefix
      }
    },
    computed: {
      _value: {
        get() {
          if(this.singleDatePicker) {
            return this.value;
          } else if(Array.isArray(this.value)){
            return this.value.join(' ~ ');
          } 
        },
        set(val) {
          if(!this.singleDatePicker){
            if('' == val) {
              val = [];
            } else{
              val = val.split(' ~ ');
            }
          }
          this.$emit('input', val);
        }
      },
    },
    watch: {
      value (val) {
        let startTime = ( this.getStartTime() == '' ? new Date() : this.getStartTime() );
        let endTime = ( this.getEndTime() == '' ? new Date() : this.getEndTime() );
        this.setDate(startTime, endTime);
      }
    },
    methods: {
      getStartTime () {
        if(this.singleDatePicker) {
          return this.value;
        } else{
          if(Array.isArray(this.value)) {
            if(this.value[0]) return this.value[0];
            else return '';
          } else{
            return this.value;
          }
        }
      },
      getEndTime () {
        if(this.singleDatePicker) {
          return this.value;
        } else{
          if(Array.isArray(this.value)) {
            if(this.value[1]) return this.value[1];
            else return '';
          } else{
            return this.value;
          }
        }
      },
      setDate (startTime, endTime) {
        let self = this;
        startTime = (moment(startTime).format(self.format));
        endTime = (moment(endTime).format(self.format));
        let o = $(self.currentNode).data('daterangepicker');
        startTime && o.setStartDate(startTime);
        endTime && o.setEndDate(endTime);
      }
    },
    mounted () {
      let self = this;
      self.currentNode = self.$refs.reservationtime.$el.querySelector('input');

      // 
      require(['bootstrap-daterangepicker/daterangepicker.scss', 'bootstrap-daterangepicker'], function() {
        let startTime = ( self.getStartTime() == '' ? new Date() : self.getStartTime() );
        let endTime = ( self.getEndTime() == '' ? new Date() : self.getEndTime() );
        let aRanges = self.ranges;
        let oRange = {};
        aRanges.forEach((v)=>{
          oRange[v.title] = [moment().subtract(v.count, 'days'), moment()];
        });
        let options = {
          startDate: startTime,
          endDate: endTime,
          timePicker: self.timePicker, // 
          autoUpdateInput: false, // 
          singleDatePicker: self.singleDatePicker,
          showDropdowns: self.showDropdowns, // 
          showCustomRangeLabel: false,
          alwaysShowCalendars: true,
          opens: self.opens,
          locale: { 
            cancelLabel: '',
            applyLabel: '',
            customRangeLabel: '',
            format: self.format,
            daysOfWeek : [ '', '', '', '', '', '', '' ],  
            monthNames : [ '', '', '', '', '', '','', '', '', '', '', '' ],  
          }
        };
        aRanges.length && (options.ranges = oRange);
        if("" != self.minDate) {
          options.minDate = self.minDate;
        }
        if("" != self.maxDate) {
          options.maxDate = self.maxDate;
        }

        $(self.currentNode).daterangepicker(options);

        $(self.currentNode).on('apply.daterangepicker', function(ev, picker) {
          if(self.singleDatePicker) {
            self._value = picker.startDate.format(self.format);
          } else{
            self._value = picker.startDate.format(self.format) + ' ~ ' + picker.endDate.format(self.format);
          }
        });

        $(self.currentNode).on('cancel.daterangepicker', function(ev, picker) {
          self._value = '';
        });

        $(self.currentNode).on('show.daterangepicker', function(ev, picker) {
          $(document).on('scroll.daterangepicker', function() {
            $(self.currentNode).blur();
            picker.hide();
          });
        });

        $(self.currentNode).on('hide.daterangepicker', function(ev, picker) {
          $(document).off('scroll.daterangepicker');
        });
      });
    },
    beforeDestroy () {
      let self = this;
      let o = $(self.currentNode).data('daterangepicker');
      o.remove();
    }
  }
</script>


