<template>
  <transition :name="transitionName">
    <div :class="classes" :style="style">
      <div :class="[`${baseClass}-content`]" ref="content">
        <div v-html="content"></div>
        <button v-if="closable" type="button" class="close" @click="close"></button>
      </div>
    </div>
  </transition>
</template>
<script>
  import * as Util from "../../../utils";
  export default{
    name: "v-notice",
    props: {
      prefixCls: {
        type: String,
        default: ''
      },
      duration: {
        type: Number,
        default: 1.5
      },
      content: {
        type: String,
        default: ''
      },
      style: {
        type: Object,
        default: function() {
          return {}
        }
      },
      closable: {
        type: Boolean,
        default: false
      },
      className: {
        type: String
      },
      transitionName: {
        type: String,
        default: ''
      },
      key: {
        type: String,
        default: '',
        required: true
      },
      onClose: {
        type: Function,
        default () {
          return new Function();
        }
      }
    },
    data () {
      return {
        closeTimer: null
      }
    },
    computed: {
      baseClass () {
        return `${this.prefixCls}-notice`;
      },
      classes () {
        return [
          this.baseClass,
          {
            [`${this.className}`]: !!this.className
          }
        ]
      },
      contentClasses () {
        return `${this.baseClass}-content`;
      }
    },
    beforeDestory () {
      this.clearCloseTimer();
    },
    methods: {
      clearCloseTimer () {
        if(this.closeTimer) {
          clearTimeout(this.closeTimer);
          this.closeTimer = null;
        }
      },
      close () {
        this.clearCloseTimer();
        this.onClose();
        this.show = false;
      }
    },
    mounted () {
      this.clearCloseTimer();
      if(this.duration) {
        this.closeTimer = setTimeout(() => {
          this.close();
        }, this.duration * 1000);
      }
    },
  }
</script>


