<template>
  <div :class="wrapperCls">
    <slot></slot>
    <template v-if="!$slots.default">
      <v-radio
        v-for="item in list"
        :type="type"
        :disabled="disabled||item.disabled"
        :text="item.text"
        :label="item.label"
        :name="item.name"
      ></v-radio>
    </template>
  </div>
</template>
<script>
  const prefix = 'v-radio-group';
  import emitter from '../base/mixins/emitter';
  import vRadio from './Radio.vue';
  export default {
    name: prefix,
    components: { vRadio },
    mixins: [emitter],
    props: {
      value: {},
      type: {
        type: String,
        default: ''
      },
      disabled: Boolean,
      list: {
        type: Array,
        default () {
          return [];
        }
      }
    },
    data:function(){
      return {
        wrapperCls: prefix
      };
    },
    watch: {
      value (val) {
        this.$emit('change', val);
        this.broadcast('v-radio', 'initData', [val]);
        this.dispatch('v-form-item', 'formItem.change', [val]);
      },
    },
    mounted() {
      this.broadcast('v-radio', 'initData', [this.value]);

      if(this.type) {
        this.broadcast('v-radio', 'initType', [this.type]);
      }

      if(this.disabled) {
        this.broadcast('v-radio', 'initDisabled', [this.disabled]);
      }
    }
  }
</script>


