<template>
<transition-group tag="div" class="v-upload-list" :class="className" name="upload-fade">
  <div class="v-upload-list-item" v-for="item in list" :class="item.status" key="item.uid">
    <div class="v-upload-list-item-info">
      <a v-if="'image' == listType" class="v-upload-list-item-thumbnail" :href="item.url" target="_blank">
        <img :src="item.url">
        <div class="v-upload-list-item-thumbnail-error" v-if="item.status=='error'">
          <v-icon type="times"/>
        </div>
        <div class="v-upload-list-item-thumbnail-success" v-if="item.status=='done'">
          <v-icon type="check"/>
        </div>
      </a>
      <v-icon v-else type="paperclip"/>
      <a :href="item.url" target="_blank" class="v-upload-list-item-name">{{item.name}}</a>
      <v-icon type="times" @click="handleClose(item)"/>
    </div>
    <div class="v-upload-list-item-progress">
      <v-progress
        :show-info="showInfo"
        :percent="item.percent"
        :stroke-width="strokeWidth"
      ></v-progress>
    </div>
  </div>
</transition-group>
</template>

<script>
import vProgress from '../progress';
import vIcon from '../icon';
export default {
  components: { vIcon, vProgress },
  props: {
    listType: {
      type: String,
      default: 'image'
    },
    list: {
      type: Array,
      default: function() {
        return []
      }
    }
  },
  data () {
    return {
      showInfo: false,
      strokeWidth: 6
    }
  },
  computed: {
    className () {
      return this.listType == 'image' ? 'v-upload-list-image' : 'v-upload-list-text';
    }
  },
  methods: {
    handleClose (file) {
      this.$emit('del', file);
    }
  }
}
</script>



