<template>
  <div :class="wrapCls">
    <span class="v-input v-input-with-icon">
      <v-input v-model="currentValue" @enter="changeHandle"></v-input>
      <i class="fa fa-search" @click="changeHandle"></i>
    </span>
  </div>
</template>
<script>
  const prefix = 'v-search';
  import vInput from '../input';
  import vButton from '../button';
  export default {
    name: prefix,
    components: { vInput, vButton },
    props: {
      value: {},
      placeholder: {
        type: String,
        default: ''
      }
    },
    computed: {
      wrapCls () {
        return [
          `${prefix}`
        ];
      },
      currentValue: {
        get () {
          return this.value;
        },
        set (val) {
          this.$emit('input', val);
        }
      }
    },
    methods: {
      changeHandle () {
        this.$emit('search', this.currentValue);
      }
    }
  }
</script>


