<template>
  <label :class="wrapperCls">
    <span :class="inputCls">
      <input
        type="radio"
        :class="originalCls"
        :value="label"
        v-model="computed_value"
        :name="name"
        :disabled="computed_disabled" />
      <span :class="innerCls"></span>
    </span>
    <span :class="labelCls">
      <slot></slot>
      <template v-if="!$slots.default">{{text}}</template>
    </span>
  </label>
</template>
<script>
  const prefix = 'v-radio';
  import emitter from '../base/mixins/emitter';
  export default {
    name: prefix,
    mixins: [emitter],
    props: {
      value: {
        type: [String, Number],
        default: ''
      },
      type: {
        type: String,
        default: ''
      },
      label: {
        type: [String, Number],
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      name: String,
      text: String,
    },
    data() {
      return {
        isGroup: false,
        computed_value: this.value,
        computed_type: this.type || '',
        computed_disabled: this.disabled
      };
    },
    watch: {
      type (val) {
        this.computed_type = val || '';
      },
      disabled (val) {
        this.computed_disabled = val;
      },
      value(val) {
        this.computed_value = val;
      },
      computed_value(val) {
        if (this.isGroup) {
          this.dispatch('v-radio-group', 'input', [val]);
        } else {
          this.$emit('input', val);
          this.$emit('change', val);
        }
      },
    },
    computed: {
      typeCls () {
        return this.computed_type ? `-${this.computed_type}` : '';
      },
      wrapperCls () {
        return [
          `${prefix}${this.typeCls}`,
          {
            [`${prefix}${this.typeCls}-checked`]: (this.computed_value == this.label),
            [`${prefix}${this.typeCls}-disabled`]: this.computed_disabled 
          }
        ]
      },
      inputCls () {
        return `${prefix}${this.typeCls}-input`;
      },
      originalCls () {
        return `${prefix}-original`;
      },
      innerCls () {
        return `${prefix}${this.typeCls}-inner`;
      },
      labelCls () {
        return `${prefix}${this.typeCls}-label`;
      },
    },
    created() {
      this.$on('initData', data => {
        this.isGroup = true;
        this.computed_value = data;
      });
      this.$on('initType', data => {
        this.computed_type = data;
      });
      this.$on('initDisabled', data => {
        this.computed_disabled = data;
      });
    },
  }
</script>


