import './message.scss';
import Notification from '../base/notification';
import * as Util from "../../utils";

const prefixCls = 'v-message';
const prefixKey = 'v_message_key_';

const oIconType = {
  'info': 'info',
  'success': 'check',
  'warning': 'warning',
  'error': 'ban',
  'loading': 'rotate-right'
};
let defaultDuration = 1.5;
let top;
let messageInstance;
let key = 1;

function getMessageInstance () {
  messageInstance = messageInstance || Notification.newInstance({
    prefixCls: prefixCls,
    style: {
      top: `${top}px`
    }
  });
  return messageInstance;
}

function notice (type, options) {
  let _options = Object.assign({
    title: '',
    content: '',
    duration: defaultDuration,
    key: `${prefixKey}${key}`,
    closable: false,
    onClose: function () {}
  }, options);
  const iconType = oIconType[type];
  // if loading
  const loadCls = type === 'loading' ? ' v-load-loop' : '';
  let instance = getMessageInstance();
  instance.notice({
    key: _options.key,
    duration: _options.duration,
    className: `${prefixCls}-${type}`,
    style: {},
    transitionName: 'move-up',
    className: `${type}`,
    closable: _options.closable,
    content: `
      <div class="${prefixCls}-content">
        <div class="${prefixCls}-content-icon"><i class="fa fa-${iconType}${loadCls}"></i></div>
        <p class="${prefixCls}-content-desc">${_options.content}</p>
      </div>
    `,
    onClose: _options.onClose
  })

  return (function () {
    let target = key++;
    return function () {
      instance.remove(`${prefixKey}${target}`);
    }
  })();
}

export default {
  info (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        content: options
      };
    } else{
      _options = options;
    }
    return notice('info', _options);
  },
  success (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        content: options
      };
    } else{
      _options = options;
    }
    return notice('success', _options);
  },
  error (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        content: options
      };
    } else{
      _options = options;
    }
    return notice('error', _options);
  },
  warning (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        content: options
      };
    } else{
      _options = options;
    }
    return notice('warning', _options);
  },
  loading (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        content: options
      };
    } else{
      _options = options;
    }
    return notice('loading', _options);
  },
  config (options) {
    if(options.top) {
      top = options.top;
    }
    if(options.duration) {
      defaultDuration = options.duration;
    }
  },
  destroy () {
    let instance = getMessageInstance();
    messageInstance = null;
    instance.destroy();
  }
}


