/**
  weiluxia
  2016.09.13
*/
export function on(el, event, cb, useCapture = false) {
  el.addEventListener(event, cb, useCapture);
}

/**
 * Remove event listener shorthand.
 *
 * @param {Element} el
 * @param {String} event
 * @param {Function} cb
 */
export function off(el, event, cb = false) {
  el.removeEventListener(event, cb);
}




