<!--
  
  weiluxia 2016.07.26
  name checkbox
  value checkbox
  checked 
  disabled 
  onChange 
-->
<template>
  <label :class="wrapClasses">
    <input type="checkbox" style="display:none" 
      :name="name"
      :disabled="disabled"
      v-model="computed_value"
      @change="handleChange"
    >
    <span class="v-switch-inner">
      <template v-if="showSlot">
        <slot v-if="computed_value" name="checked"></slot>
        <slot v-else name="unChecked"></slot>
      </template>
      <template v-else>
        <template v-if="computed_value">
          {{ checkedTxt }}
        </template>
        <template v-else>
          {{ unCheckedTxt }}
        </template>
      </template>
    </span>
  </label>
</template>
<script>
  import * as Util from "../../utils";
  import emitter from '../base/mixins/emitter';
  const prefix = 'v-switch';
  export default{
    name: prefix,
    mixins: [emitter],
    props: {
      value: {},
      name: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      checkedTxt: {
        type: String,
        default: ''
      },
      unCheckedTxt: {
        type: String,
        default: ''
      },
    },
    computed: {
      wrapClasses () {
        return [
          `${prefix}`,
          {
            'v-switch-checked': this.computed_value,
            'v-switch-disabled': this.disabled
          }
        ];
      },
      computed_value: {
        get() {
          return this.value;
        },
        set(val) {
          this.$emit('input', val);
          this.dispatch('v-form-item', 'formItem.change', [val]);
        }
      },
      showSlot () {
        return !!Util.getLen(this.$slots);
      }
    },
    methods: {
      handleChange () {
        this.$emit('change', event.currentTarget.checked);
      }
    }
  }
</script>


