<template>
  <div :class="classes" :style="style">
    <notice
      v-for="item in notices"
      :prefix-cls="prefixCls"
      :style="item.style"
      :content="item.content"
      :duration="item.duration"
      :closable="item.closable"
      :key="item.key"
      :transition-name="item.transitionName"
      :class-name="item.className"
      :on-close="item.onClose"
    ></notice>
  </div>
</template>
<script>
  import notice from './Notice.vue';
  const prefixCls = 'v-notification';
  let seed = 0
  const now = Date.now()
  function getUid () {
    return 'notification_' + now + '_' + (seed++);
  }
  export default{
    name: "v-notifycation",
    components: { notice },
    props: {
      prefixCls: {
        type: String,
        default: prefixCls
      },
      style: {
        type: Object,
        default: function () {
          return {}
        }
      },
      content: {
        type: String
      },
      className: {
        type: String
      }
    },
    data () {
      return {
        notices: []
      }
    },
    computed: {
      classes () {
        return [
          `${this.prefixCls}`,
          {
            [`${this.className}`]: !!this.className
          }
        ]
      }
    },
    methods: {
      add (notice) {
        const self = this
        const key = notice.key = notice.key || getUid();
        let notices = this.notices;
        let _notice = Object.assign({
          style: {},
          content: '',
          duration: 1.5,
          closable: false,
          key: key
        }, notice);
        _notice.onClose = function() {
          notice.onClose && notice.onClose();
          self.remove(_notice.key);
        }
        this.notices = notices.concat(_notice);
      },
      remove (key) {
        let notices = this.notices;
        notices.some((v, i) => {
          if (v.key === key) {
            this._close(v);
            this.notices.splice(i, 1);
          }
          return v.key === key;
        });
      },
      _close (notice) {
        notice.show = false;
      }
    }
  }
</script>


