<template>
  <span>
    <ul v-if="data && data.length" :class="[prefixCls + '-menu']">
      <v-casitem
        v-for="item in data"
        :prefix-cls="prefixCls"
        :data="item"
        :tmp-item="tmpItem"
        @click.native.stop="handleClickItem(item)"
        @mouseenter.native.stop="handleHoverItem(item)">
      </v-casitem>
    </ul>
    <v-caspanel
      v-if="sublist && sublist.length"
      ref="caspanel"
      :prefix-cls="prefixCls" 
      :data="sublist" 
      :disabled="disabled"
      :trigger="trigger"
      :change-on-select="changeOnSelect"
      @on-result-change="onResultChange">
    </v-caspanel>
  </span>
</template>
<script>
  import vCasitem from './Casitem.vue';
  import emitter from '../base/mixins/emitter';
  const prefix = 'v-caspanel';
    export default {
      name: prefix,
      components: { vCasitem },
      mixins: [emitter],
      props: {
        data: {
          type: Array,
          default () {
            return [];
          }
        },
        disabled: Boolean,
        changeOnSelect: Boolean,
        trigger: String,
        prefixCls: String
      },
      data () {
        return {
          tmpItem: {},
          result: [],
          sublist: []
        };
      },
      methods: {
        handleClickItem (item) {
          if (this.trigger !== 'click' && item.children) return;
          this.handleTriggerItem(item);
        },
        handleHoverItem (item) {
          if (this.trigger !== 'hover' || !item.children) return;
          this.handleTriggerItem(item);
        },
        handleTriggerItem (item, fromInit = false) {
          if (item.disabled) return;
          const backItem = this.getBaseItem(item);
          this.tmpItem = backItem;
          this.emitUpdate([backItem]);
          let lastValue = false;
          if (item.children && item.children.length) {
            let curCaspanel = this;
            if(!this.sublist.length) {
              this.sublist = item.children;
            } else{
              while (curCaspanel.sublist.length) {
                curCaspanel.sublist = [];
                curCaspanel = curCaspanel.$refs.caspanel;
              }
            }
          } else {
            this.sublist = [];
            lastValue = true;
          }
          if(item.noSelected) return;
          this.$emit('on-result-change', {
            lastValue: lastValue, 
            changeOnSelect: this.changeOnSelect, 
            fromInit: fromInit
          });
        },
        onResultChange (params) {
          this.$emit('on-result-change', params);
        },
        updateResult (item) {
          this.result = [this.tmpItem].concat(item);
          this.emitUpdate(this.result);
        },
        emitUpdate (result) {
          this.$parent.updateResult(result);
        },
        getBaseItem (item) {
          let backItem = Object.assign({}, item);
          if (backItem.children) {
            delete backItem.children;
          }
          return backItem;
        },
        clearSelect () {
          this.sublist = [];
          this.tmpItem = {};
        },
        initSelect (val) {
          let value = [...val];
          for (let i = 0; i < value.length; i++) {
            for (let j = 0; j < this.data.length; j++) {
              if (value[i] === this.data[j].value) {
                this.handleTriggerItem(this.data[j], true);
                value.splice(0, 1);
                this.$nextTick(() => {
                  this.$refs.caspanel && this.$refs.caspanel.initSelect(value);
                });
                return false;
              }
            }
          }
        }
      },
      watch: {
        data () {
          this.sublist = [];
        }
      }
    };
</script>



