let index = 0;
const now = +(new Date());
function uid() {
  return 'upload-' + now + '-' + (++index);
}

function fileToObject (file) {
  return {
    lastModified: file.lastModified,
    lastModifiedDate: file.lastModifiedDate,
    name: file.filename || file.name,
    size: file.size,
    type: file.type,
    uid: file.uid,
    response: file.response,
    percent: file.percent,
    error: file.error,
    url: file.url
  }
}

function getFileItem (file, fileList) {
  const matchWay = (!file.uid) ? 'byName' : 'byUid';
  let target = fileList.filter(item => {
    if (matchWay === 'byName') {
      return item.name === file.name;
    } else {
      return item.uid === file.uid;
    }
  })[0]

  return target;
}

// url BLOBimgurl
function createObjectURL (blob) {
  if(window.URL) {
    return window.URL.createObjectURL(blob);
  } else if(window.webkitURL) {
    return window.webkitURL.createObjectURL(blob);
  } else{
    return null;
  }
}
function revokeObjectURL (url) {
  if(window.URL) {
    return window.URL.revokeObjectURL(url);
  } else{
    return window.webkitURL.revokeObjectURL(url);
  }
}

import request from './request';
import * as Util from "../../utils";
import emitter from '../base/mixins/emitter';
import vIcon from '../icon';
import vButton from '../button';
import message from "../message";
import vProgress from '../progress';

export default {
  components: { vIcon, vButton, vProgress },
  mixins: [ emitter ],
  props: {
    value: null,
    disabled: {
      type: Boolean,
      default: false
    },
    name: {
      type: String,
      default: ''
    },
    multiple: {
      type: Boolean,
      default: false
    },
    action: {
      type: String,
      default: ''
    },
    data: {
      type: Object,
      default () {
        return {}
      }
    },
    accept: {
      type: String,
      default: 'image/*'
    },
    maxSize: {
      type: Number,
      default: 1024 // K
    },
    fileList: {
      type: Array,
      default () {
        return []
      }
    },
    beforeUpload: null,
    beforeDel: null,
    onChange: {
      type: Function,
      default () {
        return new Function();
      }
    },
    forceAjax: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      computed_fileList: this.value,
    };
  },
  watch: {
    value (val) {
      this.computed_fileList = val;
    },
    computed_fileList (val) {
      this.$emit('input', val);
      this.dispatch('v-form-item', 'formItem.change', [val]);
    }
  },
  computed: {
    computed_accept () {
      const accept = this.accept;
      if(-1 < accept.indexOf('image')) {
        return 'image/jpg,image/jpeg,image/png,image/gif';
      } else return accept;
    }
  },
  methods: {
    handleDrop() {
      this.dragOver = false;
      this.uploadFiles(event.dataTransfer.files);
    },
    handleClick () {
      this.$refs.file.click();
    },
    handleChange () {
      const files = event.target.files;
      if (!files) {
        return;
      }
      this.uploadFiles(files);
      this.$refs.file.value = null;
    },
    checkFile (file) {
      var size = file.size;
      // 
      if(size > (this.maxSize * 1024)) {
        message.error({
          closable: true,
          content: ''
        });
        return false;
      }
      return true;
    },
    checkImage (val) {
      return val.indexOf('image') !== -1;
    },
    uploadFiles (files) {
      const len = files.length;
      for(let i = 0; i < len; i++) {
        const file = files.item(i);
        if(this.checkFile(file)) {
          file.uid = uid();
          this.onStart(file);
          this.upload(file);
        }
      }
    },
    upload (file) {
      if (!this.beforeUpload) {
        return this.post(file);
      }

      const before = this.beforeUpload(file);
      this.$nextTick(() => {
        if (before && before.then) {
          before.then(() => {
            this.post(file);
          })
        } else if (before !== false) {
          this.post(file);
        }
      });
    },
    post (file) {
      let self = this;
      let data = self.data
      if (typeof data === 'function') {
        data = data();
      }

      request({
        action: self.action,
        filename: self.name,
        file: file,
        data: data,
        onProgress: e => {
          self.onProgress(e, file);
        },
        onSuccess: ret => {
          self.onSuccess(ret, file);
        },
        onError: (err, ret) => {
          self.onError(err, ret, file);
        },
      });
    },
    onStart (file) {
      let url = createObjectURL(file);
      let targetItem;
      if(!this.multiple) {
        this.computed_fileList = [];
      }
      let nextFileList = this.computed_fileList.concat();
      targetItem = fileToObject(file);
      targetItem.status = 'uploading';
      targetItem.url = url;
      nextFileList.push(targetItem);
      this.change({
        file: targetItem,
        fileList: nextFileList
      });
    },
    onProgress (e, file) {
      let fileList = this.computed_fileList;
      let targetItem = getFileItem(file, fileList);
      targetItem.percent = e.percent;
      if(targetItem) {
        this.change({
          event: e,
          file: file,
          fileList: this.computed_fileList
        });
      }
    },
    onSuccess (response, file) {
      //  json 
      // 
      try {
        if (typeof response === 'string') {
          JSON.parse(response)
        }
      } catch (e) {
        this.onError(new Error('No response'), response, file);
        return;
      }
      let fileList = this.computed_fileList;
      let targetItem = getFileItem(file, fileList);
      revokeObjectURL(targetItem.url);

      if(0 !== response.code) {
        // message.error({
        //   closable: true,
        //   content: response.message
        // });
        this.onError(response.message, response, targetItem);
        return;
      }

      // 
      if (targetItem) {
        targetItem.status = 'done';
        targetItem.response = response;
        targetItem.url = response.data.filePath;
        targetItem.name = response.data.fileName;

        this.change({
          file: targetItem,
          fileList: fileList
        });
      }
    },
    onError (error, response, file) {
      let fileList = this.computed_fileList;
      let targetItem = getFileItem(file, fileList);
      targetItem.error = error;
      targetItem.response = response;
      targetItem.status = 'error';
      // message.error({
      //   closable: true,
      //   content: error
      // });
      setTimeout(()=>{
        this.handleRemove(targetItem);
      }, 3000);
    },
    onRemove (file) {
      let self = this;
      function del () {
        file.status = 'removed';
        self.handleRemove(file);
      }
      if (!this.beforeDel) {
        del();
        return;
      }
      const before = this.beforeDel(file);
      this.$nextTick(() => {
        if (before && before.then) {
          before.then(() => {
            del();
          })
        } else if (before !== false) {
          del();
        }
      });
    },
    handleRemove (file) {
      let fileList = this.removeFile(file);
      if (fileList) {
        this.change({
          file: file,
          fileList: fileList
        })
      }
    },
    removeFile (file) {
      let fileList = this.computed_fileList;
      let targetItem = getFileItem(file, fileList);
      let index = fileList.indexOf(targetItem);
      revokeObjectURL(targetItem.url);
      
      if (index !== -1) {
        fileList.splice(index, 1)
        return fileList;
      }

      return null;
    },
    getFilePlainObject (fileList) {
      let arr = []
      fileList.forEach(item => {
        arr.push({
          name: item.name,
          size: item.size,
          status: item.status,
          type: item.type,
          uid: item.uid,
          response: item.response,
          url: item.url,
          percent: item.percent
        })
      })
      return arr;
    },
    change (info) {
      // 1.  fileList
      // 2. info.event fileList
      if (!info.event && info.fileList) {
        let fileList = info.fileList;
        // vue
        fileList = this.getFilePlainObject(fileList);
        this.computed_fileList = fileList;
      }
      this.$emit('change', info);
      this.onChange(info);
    },
  }
}



