<!--
  
  weiluxia 2016.07.24
  show 
  confirm-loading  loading
  title 
  on-ok 
  on-cancel 
  width   String or Number  600
  ok-text   String  
  cancel-text 
  showOk 
  showCancel 
  closable  Esc 
  maskClosable 
  footerHide footer
-->
<template>
  <div :class="wrapClasses">
    <transition name="dialog-fade">
      <div class="v-modal-mask" v-show="value" @click="maskHandle"></div>
    </transition>
    <transition name="dialog-move-up" @after-leave="doDestroy">
      <div class="modal-dialog" v-show="value" :style="styles">
        <div class="modal-content">
          <div class="modal-header" v-if="showHead">
            <button v-if="closable" type="button" class="close" @click="closeHandle"><span></span></button>
            <h4 class="modal-title" v-html="title"></h4>
          </div>
          <div class="modal-body" ref="dialog">
            <slot></slot>
          </div>
          <div class="modal-footer" v-if="!footerHide">
            <v-button type="ghost" v-if="cancelBtn" @click="closeHandle">
              {{cancelText}}
            </v-button>
            <v-button type="info" :loading="confirmLoading" v-if="okBtn" @click="okHandle">
              {{okText}}
            </v-button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
  import * as Util from "../../../utils";
  import vButton from '../../button';
  export default{
    name: 'v-dialog',
    components: { vButton },
    props: {
      value: Boolean,
      prefixCls: {
        type: String,
        default: ''
      },
      closable: {
        type: Boolean,
        default: true
      },
      maskClosable: {
        type: Boolean,
        default: true
      },
      show: {
        type: Boolean,
        default: false
      },
      footerHide: {
        type: Boolean,
        default: false
      },
      okBtn: {
        type: Boolean,
        default: true
      },
      cancelBtn: {
        type: Boolean,
        default: true
      },
      loading: {
        type: Boolean,
        default: false
      },
      confirmLoading: {
        type: Boolean,
        default: false
      },
      type: {
        type: String,
        default: ''
      },
      title: {
        type: String,
        default: ''
      },
      width: {
        type: Number,
        default: 520
      },
      cancelText: {
        type: String,
        default: ''
      },
      okText: {
        type: String,
        default: ''
      },
      style: {
        type: Object,
        default () {
          return {};
        }
      },
      wrapClassName: {
        type: String,
        default: ''
      }
    },
    data () {
      return {
        wrapShow: false,
        showHead: true
      }
    },
    computed: {
      wrapClasses () {
        return [
          this.prefixCls,
          'thinScroll',
          {
            [`${this.wrapClassName}`]: !!this.wrapClassName,
            'show': this.wrapShow,
            'hide': !this.wrapShow
          }
        ];
      },
      styles () {
        let style = {};
        const styleWidth = {
          width: `${this.width}px`
        };
        const customStyle = !!this.style ? this.style : {};
        Object.assign(style, styleWidth, customStyle);
        return style;
      }
    },
    watch: {
      value (val) {
        if (val) {
          this.open();
          this.$emit('open');
          this.$nextTick(() => {
            this.$refs.dialog.scrollTop = 0;
          });
        } else{
          this.close();
          this.$emit('close');
        }
      }
    },
    beforeDestroy () {
      document.removeEventListener('keydown', this.EscClose);
    },
    methods: {
      doDestroy () {
        this.wrapShow = false;
      },
      open () {
        let self = this;
        self.wrapShow = true;
        document.body.addEventListener('keydown', self.escClose, false);
      },
      close () {
        let self = this;
        document.body.removeEventListener('keydown', self.escClose, false);
      },
      maskHandle () {
        if(this.maskClosable) this.closeHandle();
      },
      escClose (e) {
        if(this.normalizedShow && this.closable) {
          if(e.keyCode === 27) this.closeHandle();
        }
      },
      closeHandle () {
        this.$emit('input', false);
        this.$emit('cancel');
        this.close();
      },
      okHandle () {
        this.$emit('save');
        if(!this.loading) {
          this.$emit('input', false);
          this.close();
        }
      },
    },
    mounted () {
      let showHead = true;
      if(!this.title) showHead = false;
      this.showHead = showHead;
      this.value && this.open();
    }
  }
</script>


