import Vue from 'vue';
import * as Util from "../../utils";
import vModal from './Modal.vue';
import vButton from '../button';

const prefixCls = 'v-modal-confirm';
const oIconType = {
  'info': 'info-circle',
  'success': 'check-circle',
  'warning': 'warning',
  'error': 'times-circle',
  'confirm': 'question-circle'
};
vModal.newInstance = properties => {
  const _props = properties || {};
  let props = ''; 
  Object.keys(_props).forEach(prop => {
    props += ' :' + Util.camelcaseToHyphen(prop) + '=' + prop; 
  });
  var Profile = Vue.extend({
    components: { vModal, vButton },
    template: `<v-modal ${props} v-model="show" :width="width" @toggle="toggleShow">
                <div class="${prefixCls}">
                  <div class="${prefixCls}-head">
                    <div :class="iconTypeCls"><i :class="iconNameCls"></i></div>  
                    <div class="${prefixCls}-head-title" v-html="title"></div>   
                  </div>  
                  <div class="${prefixCls}-body" v-html="body"></div>   
                  <div class="${prefixCls}-footer">
                    <button v-if="cancelBtn" type="button" class="btn v-button btn-ghost" @click="cancel">{{ cancelText }}</button>
                    <button type="button" class="btn v-button btn-info" @click="ok">{{ okText }}</button>
                  </div>   
                </div>   
              </v-modal>`,
    data () {
      return Object.assign(_props, {
        show: false,
        width: 400,
        title: '',
        body: '',
        iconType: '',
        iconName: '',
        okText: '',
        cancelText: '',
        cancelBtn: false,
        okBtn: true,
        loading: false,
        confirmLoading: false,
        onOk: function() {},
        onCancel : function () {},
        onRemove: function () {}
      })
    },
    computed: {
      iconTypeCls () {
        return [
          `${prefixCls}-head-icon`,
          `${prefixCls}-head-icon-${this.iconType}`
        ]
      },
      iconNameCls () {
        return [
          'fa',
          `fa-${this.iconName}`
        ]
      }
    },
    methods: {
      cancel () {
        this.show = false;
        this.confirmLoading = false;
        this.onCancel();
        this.remove();
      },
      ok () {
        if(this.loading) {
          this.confirmLoading = true;
        } else{
          this.show = false;
          this.remove();
        }
        this.onOk();
      },
      remove () {
        setTimeout(() => {
          this.destroy();
        }, 200);
      },
      destroy () {
        this.$destroy();
        document.body.removeChild(modal.$el);
        this.onRemove();
      },
      toggleShow (flag) {
        this.show = flag;
      },
      onInput (val) {
        this.show = val;
      },
    }
  })
  //  Profile 
  // $mount:
  var modal = new Profile().$mount();
  document.body.appendChild(modal.$el);

  return {
    component: modal,
    show (props) {
      modal.cancelBtn = props.cancelBtn;
      modal.iconType = props.icon;
      modal.iconName = oIconType[props.icon];

      if ('width' in props) {
        modal.width = props.width;
      }
      if ('title' in props) {
        modal.title = props.title;
      }
      if ('content' in props) {
        modal.body = props.content;
      }
      if ('okText' in props) {
        modal.okText = props.okText;
      }
      if ('cancelText' in props) {
        modal.cancelText = props.cancelText;
      }
      if ('onCancel' in props) {
        modal.onCancel = props.onCancel;
      }
      if ('onOk' in props) {
        modal.onOk = props.onOk;
      }
      // async for ok
      if ('loading' in props) {
        modal.loading = props.loading;
      }

      // notice when component destroy
      modal.onRemove = props.onRemove;
      modal.show = true;
    },
    toggleLoading (flag) {
      modal.confirmLoading = flag;
    },
    remove () {
      modal.show = false;
      modal.loading = false;
      modal.remove();
    },
  };
};
export default vModal;


