<template>
  <div :class="wrapCls">
    <slot></slot>
  </div>
</template>
<script>
  export default{
    name: 'v-col',
    props: {
      span: [Number, String]
    },
    computed: {
      wrapCls () {
        return [
          `col-sm-${this.span}`,
        ]
      }
    }
  }
</script>


