<!--
  
  trigger 
  placement 
  show 
  title 
  content 
  onVisibleChange 
-->
<template>
  <span>
    <slot name="trigger"></slot>
    <!-- <transition :name="transition"> -->
      <div ref="popup" :class="popupCls" v-show="value" @click.stop>
        <div class="v-popover-arrow"></div>
        <div class="v-popover-inner">
          <p class="v-popover-inner-title" v-if="title" v-html="title"></p>
          <div class="v-popover-inner-desc">
            <template v-if="content">{{content}}</template>  
            <template v-else><slot name="content"></slot></template>
          </div>
        </div>
      </div>
    <!-- </transition> -->
  </span>
</template>
<script>
  import trigger from "../base/trigger/trigger";
  const prefix = 'v-popover';
  export default{
    name: prefix,
    mixins: [ trigger ],
    props: {
      title: {
        type: String,
        default: ''
      },
      content: {
        type: String,
        default: ''
      },
      trigger: {
        type: String,
        default: 'click'
      },
      transition: {
        type: String,
        default: 'fade-right'
      },
    },
    data () {
      return {
        prefixCls: prefix
      }
    }
  }
</script>


