<template>
  <div :class="wrapCls">
    <form :class="formCls">
      <slot></slot>
    </form>
    <v-loading :show="loading"></v-loading>
  </div>
</template>
<script>
  const prefix = 'v-form';
  import vLoading from '../loading';
  import emitter from '../base/mixins/emitter';
  export default{
    name: prefix,
    mixins: [emitter],
    components: {vLoading},
    props: {
      model: Object,
      rules: Object,
      defaultModel: Object,
      loading: Boolean,
      position: {
        type: String,
        default: 'horizontal'
      },
      tip: String
    },
    data () {
      return {
        fields: []
      };
    },
    computed: {
      wrapCls () {
        return [
          {
            'v-loading-container' : this.loading
          }
        ];
      },
      formCls () {
        return [
          `${prefix}`,
          'clearfix',
          {
            'form-horizontal': this.position == '' || this.position == 'horizontal',
            'v-loading-basement': this.loading
          }
        ];
      }
    },
    created() {
      const rules = this.rules;
      this.$on('form.addField', (field) => {
        if(field) {
          this.fields.push(field);
        }
      });
      this.$on('form.removeField', (field) => {
        this.fields.splice(this.fields.indexOf(field), 1);
      });
    },
    watch: {
      rules(val) {
        this.validate();
      },
    },
    methods: {
      // 
      resetFields() {
        this.fields.forEach(field => {
          field.resetField();
        });
      },
      // 
      resetFieldHints() {
        this.fields.forEach(field => {
          field.resetFieldHint();
        });
      },
      // 
      validate (callback) {
        let fields = this.fields;
        let flag = true;
        fields.some((field, index) => {
          field.validate('', valid => {
            flag = valid && flag;
          });
          if('pop' == this.tip && !flag) return !flag; 
        });
        if(typeof callback === 'function') {
          callback(flag);
        }
      }
    },
  }
</script>


