<template>
  <div :class="wrapperCls">
    <slot></slot>
    <template v-if="!$slots.default">
      <v-checkbox
        v-for="item in list"
        :type="type"
        :disabled="disabled || item.disabled"
        :text="item.text"
        :label="item.label"
        :name="item.name"
      ></v-checkbox>
    </template>
  </div>
</template>
<script>
  const prefix = 'v-checkbox-group';
  import emitter from '../base/mixins/emitter';
  import vCheckbox from './Checkbox.vue';
  export default {
    name: prefix,
    components: { vCheckbox },
    props: {
      value: {},
      type: {
        type: 'String',
        default: ''
      },
      disabled: Boolean,
      list: {
        type: Array,
        default: []
      }
    },
    mixins: [emitter],
    data:function(){
      return {
        wrapperCls: prefix
      };
    },
    watch: {
      value (val) {
        this.$emit('change', [val]);
        this.broadcast('v-checkbox', 'initData', [val]);
        this.dispatch('v-form-item', 'formItem.change', [val]);
      }
    },
    mounted() {
      this.broadcast('v-checkbox', 'initData', [this.value]);
      if(this.$slots.default) {
        this.broadcast('v-checkbox', 'initDisabled', [this.disabled]);
      }
    }
  }
</script>


