<template>
  <div :class="wrapCls">
    <label :class="labelCls" v-text="label"></label>
    <div :class="contentCls">
      <slot></slot>
      <transition name="move-top-up">
        <span :class="hintCls">
          <!-- <template v-if="ruleHint!='' && validateMessage==''">
            {{ruleHint}}
          </template> -->
          <template v-if="validateMessage!=''">
            {{validateMessage}}
          </template>
        </span>
      </transition>
    </div>
  </div>
</template>
<script>
  const prefix = 'v-form-item';
  import emitter from '../base/mixins/emitter';
  import message from "../message";
  import * as Util from "../../utils";
  function getPropByPath(obj, path) {
    let tempObj = obj;
    path = path.replace(/^\./, '');
    let keyArr = path.split('.');
    let i = 0;
    for (let len = keyArr.length; i < len - 1; ++i) {
      let key = keyArr[i];
      if (key in tempObj) {
        tempObj = tempObj[key];
      } else {
        throw new Error('please transfer a valid prop path to form item!');
      }
    }
    return {
      o: tempObj,
      k: keyArr[i],
      v: tempObj[keyArr[i]]
    };
  }
  export default{
    name: prefix,
    mixins: [emitter],
    props: {
      label: String,
      name: String,
      ruleName: String, // 
      labelCol: {
        type: [Number, String],
        default: 2
      },
      contentCol: {
        type: [Number, String],
        default: 10
      },
      rule: {
        type: Object,
        default () {
          return {};
        }
      }
    },
    data () {
      return {
        validateMessage: '',
        validateState: '',
        validateDisabled: false,
      };
    },
    computed: {
      wrapCls () {
        return [
          'form-group',
          `${prefix}`,
          {
            'is-success': 'success' == this.validateState,
            'is-error': 'error' == this.validateState,
            'is-warning': 'warning' == this.validateState,
          }
        ];
      },
      contentCls () {
        return [
          {
            [`col-sm-${this.contentCol}`]: this.position == '' || this.position == 'horizontal',
          },
          `${prefix}-content`
        ];
        return `${prefix}-content`;
      },
      hintCls () {
        return `${prefix}-hint`;
      },
      // ruleHint () {
      //   if('success' != this.validateState && 'error' != this.validateState) {
      //     this.validateState = 'warning';
      //     const rule = this.getRule();
      //     return rule.ruleHint;
      //   }
      //   return '';
      // },
      labelCls () {
        const rule = this.getRule();
        return [
          {
            [`col-sm-${this.labelCol} control-label`]: this.position == '' || this.position == 'horizontal',
            [`${prefix}-label-required`]: rule.required 
          },
          `${prefix}-label`
        ];
      },
      form() {
        var parent = this.$parent;
        while (parent.$options._componentTag !== 'v-form') {
          parent = parent.$parent;
        }
        return parent;
      },
      fieldValue: {
        cache: false,
        get() {
          let model = this.form.model;
          let name = this.name;
          if (!model || !name) { return; }
          return getPropByPath(model, name).v;
        }
      },
      position: {
        get() {
          var position = this.form.position;
          return position;
        }
      },
      tip: {
        get() {
          var tip = (this.form.tip == 'pop' ? 'pop' : '');
          return tip;
        }
      }
    },
    methods: {
      resetFieldHint () {
        this.validateState = '';
        this.validateMessage = '';
      },
      resetField () {
        this.resetFieldHint();
        let model = this.form.model;
        let name = this.name;
        if(!model || !name) { return; }
        let value = this.fieldValue;
        let prop = getPropByPath(model, name);
        if(Util.getLen(value)) {
          this.validateDisabled = true;
          if(Array.isArray(value)) {
            prop.o[prop.k] = [];
          } else{
            prop.o[prop.k] = this.initialValue;
          }
        }
      },
      getRule () {
        let formRules = this.form.rules;
        let selfRuel = this.rule;
        let ruleName = this.name;
        if(this.ruleName && '' != this.ruleName) {
          ruleName = this.ruleName;
        }
        let formRule = formRules ? formRules[ruleName] : {};
        let rule = Object.assign({
          required: false,
          trigger: 'blur',
          message: '',
          validator: null
        }, selfRuel, formRule);
        return rule;
      },
      getFilteredRule(trigger) {
        var rule = this.getRule();
        return !rule.trigger || rule.trigger.indexOf(trigger) !== -1;
      },
      validate (trigger, callback) {
        const rule = this.getRule();
        const fieldValue = this.fieldValue;
        let isOk = true, validateMessage = '';
        if(rule && this.getFilteredRule(trigger)) {
          if(rule.required || Util.getLen(fieldValue)) {
            if(Util.getLen(fieldValue)) {
              if(Util.isFunction(rule.validator)) {
                var o = rule.validator(fieldValue);
                isOk = o.isok;
                validateMessage = o.message;
              } else{
                isOk = true;
                validateMessage = '';
              }
            } else{
              isOk = false;
              validateMessage = rule.message;
            }
          } else{
            isOk = true, validateMessage = '';
          }
          this.validateState = isOk ? 'success' : 'error';
          if('pop' == this.tip && !isOk) {
            message.info({
              content: validateMessage
            });
          }
          this.validateMessage = validateMessage;
          callback && callback(isOk);
        }
      },
      onFieldChange () {
        if(this.validateDisabled) {
          this.validateDisabled = false;
          return;
        }
        this.validate('change');
      },
      onFieldBlur () {
        this.validate('blur');
      },
      onFieldFocus () {
        this.validate('focus');
      }
    },
    created () {
      if(this.name) {
        this.$on('formItem.change', data=>{
          this.onFieldChange();
        });
        this.$on('formItem.focus', data=>{
          this.onFieldFocus();
        });
        this.$on('formItem.blur', data=>{
          this.onFieldBlur();
        });
      }
    },
    mounted () {
      if(this.name) {
        this.dispatch('v-form', 'form.addField', [this]);
      }
      Object.defineProperty(this, 'initialValue', {
        value: this.fieldValue
      });
    },
    beforeDestroy() {
      if(this.name) {
        this.dispatch('v-form', 'form.removeField', [this]);
      }
    }
  }
</script>


