import './notify.scss';
import Notification from '../base/notification';
import * as Util from "../../utils";

const prefixCls = 'v-notify';
const prefixKey = 'v_notify_key_';

const oIconType = {
  'info': 'info-circle',
  'success': 'check-circle',
  'warning': 'warning',
  'error': 'times-circle',
  'loading': 'rotate-right'
};
let defaultDuration = 2;
let top;
let noticeInstance;
let key = 1;

function getnoticeInstance () {
  noticeInstance = noticeInstance || Notification.newInstance({
    prefixCls: prefixCls,
    style: {
      top: `${top}px`
    }
  });
  return noticeInstance;
}

function notice (type, options) {
  let _options = Object.assign({
    title: '',
    content: '',
    duration: defaultDuration,
    key: `${prefixKey}${key}`,
    closable: true,
    onClose: function () {}
  }, options);
  const iconType = oIconType[type];
  let instance = getnoticeInstance();
  let content;
  if (type == 'normal') {
    content = `
      <div class="${prefixCls}-content">
        <div class="${prefixCls}-title">${_options.title}</div>
        <div class="${prefixCls}-desc">${_options.content}</div>
      </div>
    `;
  } else {
    let descCls = !!_options.content ? `${prefixCls}-with-desc`: '';
    content = `
      <div class="${prefixCls}-content ${prefixCls}-with-icon ${descCls}">
        <span class="${prefixCls}-icon">
          <i class="fa fa-${iconType}"></i>
        </span>
        <div class="${prefixCls}-title">${_options.title}</div>
        <div class="${prefixCls}-desc">${_options.content}</div>
      </div>
    `;
  }
  instance.notice({
    key: _options.key,
    duration: _options.duration,
    className: `${type}`,
    style: {},
    transitionName: 'move-left',
    content: content,
    onClose: _options.onClose,
    closable: _options.closable,
  })

  return (function () {
    let target = key++;
    return function () {
      instance.remove(`${prefixKey}${target}`);
    }
  })();
}

export default {
  open (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        title: options
      };
    } else{
      _options = options;
    }
    return notice('normal', _options);
  },
  info (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        title: options
      };
    } else{
      _options = options;
    }

    return notice('info', _options);
  },
  success (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        title: options
      };
    } else{
      _options = options;
    }
    return notice('success', _options);
  },
  error (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        title: options
      };
    } else{
      _options = options;
    }
    return notice('error', _options);
  },
  warning (options) {
    let _options = {};
    if(!Util.isObject(options)) {
      _options = {
        content: options
      };
    } else{
      _options = options;
    }
    return notice('warning', _options);
  },
  config (options) {
    if(options.top) {
      top = options.top;
    }
    if(options.duration) {
      defaultDuration = options.duration;
    }
  },
  close (key) {
    if (key) {
      key = key.toString();
      if(noticeInstance) {
        noticeInstance.remove(key);
      }
    } else {
      return false;
    }
  },
  destroy () {
    let instance = getnoticeInstance();
    noticeInstance = null;
    instance.destroy();
  }
}


