function isPointsEq (a1, a2) {
  return a1[0] === a2[0] && a1[1] === a2[1];
}
const mouseEnterDelay = 0; // 
const mouseLeaveDelay = 0.1; // 

import * as Util from "../../../utils";
import oPlacements from "./placements";
import triggerMixin from "./triggerMixin";
import popupMixin from "./popupMixin";

export default{
  mixins: [popupMixin, triggerMixin],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    className: {
      type: String,
      default: ''
    },
    placement: {
      type: String,
      default: 'left'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    onVisibleChange: {
      type: Function,
      default () {
        return new Function();
      }
    }
  },
  data () {
    return {
      posCls: '',
      computed_value: this.value,
    }
  },
  computed: {
    align () {
      return oPlacements[this.placement];
    },
    popupCls () {
      return [
        this.prefixCls,
        this.posCls
      ];
    },
  },
  watch: {
    value(val) {
      this.computed_value = val;
    },
    computed_value(val) {
      if(val) {
        this.$emit('open');
      } else{
        this.$emit('close');
      }
      this.$emit('input', val);
    },
  },
  methods: {
    // TransformOrigin
    resetTransformOrigin(placement) {
      let placementMap = { top: 'bottom', bottom: 'top', left: 'right', right: 'left' };
      let origin = placementMap[placement];
      this.$refs.popup.style.transformOrigin = ['top', 'bottom'].indexOf(placement) > -1
        ? `center ${ origin }`
        : `${ origin } center`;
    },
    // popup
    insertPopup () {
      this.posCls = this.getClassNameFromAlign(oPlacements, this.prefixCls, this.align);
      document.body.appendChild(this.$refs.popup);
    },
    // popup
    alignCallback (sourceNode, align) {
      const posCls = this.posCls;
      const alignClassName = this.getClassNameFromAlign(oPlacements, this.prefixCls, align);
      if(alignClassName != posCls) {
        this.posCls = alignClassName;
      }
    },
    // placementclass
    getClassNameFromAlign (oPlacements, prefixCls, align) {
      const placement = this.placement;
      const points = align.points;
      this.resetTransformOrigin(placement);
      for (const placement in oPlacements) {
        if (oPlacements.hasOwnProperty(placement)) {
          if (isPointsEq(oPlacements[placement].points, points)) {
            return `${prefixCls}-${placement}`;
          }
        }
      }
      return '';
    },
    // triggerclick
    isClickAction () {
      if (this.trigger.indexOf('click') !== -1) {
        return true
      }
      return false
    },
    // triggerhover
    isHoverAction () {
      if (this.trigger.indexOf('hover') !== -1) {
        return true
      }
      return false
    },
    // triggerfocus
    isFocusAction () {
      if (this.trigger.indexOf('focus') !== -1) {
        return true
      }
      return false
    },
    onBlur () {
      if(!this.isFocusAction() || this.disabled) return;
      this.setPopupVisible(false);
    },
    onFocus () {
      if(!this.isFocusAction() || this.disabled) return;
      this.insertPopup();
      this.setPopupVisible(true);
    },
    onClick () {
      if(!this.isClickAction() || this.disabled) return;
      event.preventDefault();
      this.insertPopup();
      this.setPopupVisible(!this.computed_value);
    },
    onMouseEnter () {
      if(!this.isHoverAction() || this.disabled) return;
      this.insertPopup();
      this.delaySetPopupVisible(true, mouseEnterDelay);
    },
    onMouseLeave () {
      if(!this.isHoverAction() || this.disabled) return;
      this.delaySetPopupVisible(false, mouseLeaveDelay);
    },
    delaySetPopupVisible (visible, delayS) {
      const delay = delayS * 1000;
      if (this.delayTimer) {
        clearTimeout(this.delayTimer)
        this.delayTimer = null;
      }
      if (delay) {
        this.delayTimer = setTimeout(() => {
          this.setPopupVisible(visible)
          this.delayTimer = null;
        }, delay)
      } else {
        this.setPopupVisible(visible)
      }
    },
    // popup/
    setPopupVisible (visible) {
      if (this.computed_value != visible) {
        this.computed_value = visible;
        visible && this.$nextTick(this.doAlign);
      }
      this.onVisibleChange(visible);
    }
  },
  mounted () {
    const self = this;

    const trigger = this.$slots.trigger[0].elm;
    document.body.addEventListener('click', function(e) {
      if (!self.$el ||
        !trigger ||
        self.$el.contains(e.target) ||
        trigger.contains(e.target)
      ) return;
      if(self.computed_value) self.setPopupVisible(false);
    }, false);

    if(self.computed_value) {
      self.insertPopup();
      self.setPopupVisible(true);
    }
  },
  beforeDestroy () {

  }
}


