<template>
  <div class="v-progress-outer v-progress-line">
    <span class="v-progress-text" v-if="showInfo" v-html="render()">
    </span>
    <div class="v-progress-inner">
      <div class="v-progress-bg"
        :style="{width: percent + '%', height: strokeWidth + 'px', background: strokeColor}"
      ></div>
    </div>
  </div>
</template>
<script>
  import * as Util from "../../utils";
  const statusColorMap = {
    'normal': '#2db7f5',
    'error': '#f50',
    'success': '#87d068'
  }
  const prefix = 'v-progress-line';
  export default{
    name: prefix,
    props: {
      showInfo: {
        type: Boolean,
        default: true
      },
      percent: {
        type: Number,
        default: 0
      },
      strokeWidth: {
        type: Number,
        default: 10
      },
      status: {
        type: String,
        default: 'normal'
      },
      format: null
    },
    data () {
      return {
        computed_status: this.status
      }
    },
    watch: {
      status (val) {
        this.computed_status = val;
      },
      percent (val) {
        if(100 == val) {
          this.computed_status = 'success';
        } else{
          this.computed_status = 'normal';
        }
      }
    },
    computed: {
      strokeColor () {
        return statusColorMap[this.computed_status];
      },
    },
    methods: {
      // 
      render () {
        if (typeof this.format == 'function') {
          return this.format.call(this._context, this.percent);
        } else{
          return this.percent + '%';
        }
      }
    }
  }
</script>


