<template>
  <div :style="placeholderStyle">
    <div ref="fixedNode" :class="classes" :style="styles">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import * as Util from "../../utils";
  const prefix = 'v-affix';
  function getScroll(target, top) {
    // pageXOffset  X pageYOffset  Y 
    const prop = top ? 'pageYOffset' : 'pageXOffset';
    const method = top ? 'scrollTop' : 'scrollLeft';
    let ret = target[prop];
    if (typeof ret !== 'number') {//ie
      ret = window.document.documentElement[method];
    }
    return ret;
  }
  function getTargetRect(target) {
    return target !== window ?
      target.getBoundingClientRect() :
      { top: 0, left: 0, bottom: 0 };
  }
  function getOffset(element, target) {
    // 
    const elemRect = element.getBoundingClientRect(); // 
    const targetRect = getTargetRect(target);

    const scrollTop = getScroll(target, true);
    const scrollLeft = getScroll(target, false);

    const docElem = window.document.body;
    const clientTop = docElem.clientTop || 0;
    const clientLeft = docElem.clientLeft || 0;

    return {
      top: elemRect.top - targetRect.top +
      scrollTop - clientTop,
      left: elemRect.left - targetRect.left +
      scrollLeft - clientLeft,
    };
  }
  export default {
    name: prefix,
    props: {
      offsetTop: {
        type: [Number, String],
        default: 0
      },
      offsetBottom: {
        type: [Number, String],
      },
      onChange: {
        type: Function,
        default () {
          return new Function();
        }
      },
      target: {
        type: Function,
        default () {
          return (function() {
            return typeof window !== 'undefined' ? window : null;
          })();
        }
      }
    },
    data () {
      return {
        affix: false,
        placeholderStyle: null,
        styles: {}
      }
    },
    computed: {
      offsetType () {
        let type = 'top';
        if (Number(this.offsetBottom) >= 0) {
          type = 'bottom';
        }
        return type;
      },
      classes () {
        return [
          {
            [`${prefix}`]: this.affix
          }
        ]
      }
    },
    mounted () {
      this.$nextTick(function () {
        this.setTargetEventListeners();
      });
    },
    beforeDestroy () {
      this.removeTargetEventListeners();
    },
    methods: {
      // 
      setTargetEventListeners() {
        const target = this.target();
        target.addEventListener('scroll', this.handleScroll, false);
        target.addEventListener('resize', this.handleScroll, false);
      },
      // 
      removeTargetEventListeners() {
        const target = this.target();
        target.removeEventListener('scroll', this.handleScroll, false);
        target.removeEventListener('resize', this.handleScroll, false);
      },
      // 
      setPlaceholderStyle (e, placeholderStyle) {
        const originalPlaceholderStyle = this.placeholderStyle;
        if(Util.shallowequal(placeholderStyle, originalPlaceholderStyle)) return;
        this.placeholderStyle = placeholderStyle;
      },
      setAffixStyle(e, affixStyle) {
        const originalAffixStyle = this.styles;
        const isWindow = this.target() === window;
        if(Util.shallowequal(affixStyle, originalAffixStyle)) return;
        const affix = !!affixStyle;
        this.affix = affix;
        this.styles = affixStyle;
        this.$emit('on-change', affix);
        this.onChange(affix);
      },
      // 
      handleScroll () {
        if(this.$el.offsetWidth == 0 || this.$el.offsetHeight == 0) return;
        const affix = this.affix;
        const targetNode = this.target();
        const affixNode = this.$el;
        const scrollTop = getScroll(targetNode, true);
        const elemOffset = getOffset(affixNode, targetNode);
        const elemSize = {
          width: affixNode.getElementsByTagName('div')[0].offsetWidth,
          height: affixNode.getElementsByTagName('div')[0].offsetHeight,
        };
        const targetRect = getTargetRect(targetNode);
        const targetInnerHeight = targetNode.innerHeight || targetNode.clientHeight;
        if(this.offsetType == 'top') {
          if (scrollTop > (elemOffset.top - Number(this.offsetTop)) && !affix) {
            // Fixed Top
            this.setAffixStyle(event, {
              position: 'fixed',
              top:  (targetRect.top + Number(this.offsetTop)) + 'px',
              left: (targetRect.left + elemOffset.left) + 'px',
              width: `${affixNode.offsetWidth}px`,
            });
            this.setPlaceholderStyle(event, {
              width: affixNode.offsetWidth + 'px',
              height: affixNode.offsetHeight + 'px',
            });
          } else if(scrollTop < (elemOffset.top -  Number(this.offsetTop)) && affix) {
            this.setAffixStyle(event, null);
            this.setPlaceholderStyle(event, null);
          }
        } else{
          if (scrollTop < (elemOffset.top + elemSize.height + Number(this.offsetBottom) - targetInnerHeight) && !affix) {
            // Fixed Bottom
            const targetBottomOffet = targetNode === window ? 0 : (window.innerHeight - targetRect.bottom);
            this.setAffixStyle(event, {
              position: 'fixed',
              bottom: (targetBottomOffet + Number(this.offsetBottom)) + 'px',
              left: (targetRect.left + elemOffset.left) + 'px',
              width: affixNode.offsetWidth + 'px',
            });
            this.setPlaceholderStyle(event, {
              width: affixNode.offsetWidth + 'px',
              height: affixNode.offsetHeight + 'px',
            });
          } else if(scrollTop > (elemOffset.top + elemSize.height + Number(this.offsetBottom) - targetInnerHeight) && affix){
            this.setAffixStyle(event, null);
            this.setPlaceholderStyle(event, null);
          }
        }
      },
      refresh () {
        this.handleScroll();
      }
    }
  }
</script>



