<template>
<transition :name="transition">
  <div class="tab-pane" role="tabpanel"
    v-show="show"
    :class="show ?'active': ''"
  >
    <slot></slot>
  </div>
</transition>
</template>

<script>
  import * as Util from "../../utils"; 
  export default {
    name: "v-tab-item",
    props: {
      nav: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      className: {
        type: String,
        default: ''
      }
    },
    data: function() {
      return {
        index: 0
      };
    },
    computed: {
      show: function() {
        return (this.$parent.active == this.index);
      },
      transition: function() {
        return this.$parent.effect;
      }
    },
    created: function() {
      this.$parent.renderData.push({
        nav: this.nav,
        disabled: this.disabled,
        className: this.className
      });
    },
    mounted: function() {
      for(var c in this.$parent.$children) {
        if(this.$parent.$children[c].$el == this.$el) {
          this.index = c;
          break;
        }
      }
    }
  }
</script>



