<template>
  <button :type="nativeType"
  @click="onclick"
  :class="btClasses"
  > 
    <v-icon v-if="icon && !loading" :type="icon"></v-icon>
    <v-icon v-if="loading" class-name="v-load-loop" type="rotate-right"></v-icon>
    <template v-if="showSlot"><slot></slot></template>
  </button>
</template>
<script>
  import vIcon from '../icon';
  import * as Util from "../../utils"; 
  // 
  const oTypeClass = {
    'default': 'btn-default',
    'primary': 'btn-primary',
    'ghost': 'btn-ghost',
    'text': 'btn-text',
    'success': 'btn-success',
    'info': 'btn-info',
    'danger': 'btn-danger',
    'warning': 'btn-warning'
  };
  // 
  const oShapeClass = {
    'default': '',
    'flat': 'btn-flat',
    'circle': 'btn-circle'
  };
  // 
  const oSizeClass = {
    'default': '',
    'big': 'btn-lg',
    'small': 'btn-sm',
    'xSmall': 'btn-xs'
  };
  const prefix = 'v-button';
  export default{
    name: prefix,
    components: { vIcon },
    props: {
      nativeType: {
        type: String,
        default: 'button'
      },
      type: {
        type: String,
        default: 'default'
      },
      size: {
        type: String,
        default: 'default'
      },
      icon: {
        type: String,
        default: ''
      },
      shape: {
        type: String,
        default: 'default'
      },
      className: {
        type: String,
        default: ''
      },
      disabled: {
        type: Boolean,
        default: false
      },
      long: {
        type: Boolean,
        default: false
      },
      loading: {
        type: Boolean,
        default: false
      },
    },
    data () {
      return {
      }
    },
    computed: {
      btClasses () {
        return [
          'btn',
          prefix,
          this.className,
          oTypeClass[this.type],
          oSizeClass[this.size],
          oShapeClass[this.shape],
          {
            ['btn-block']: this.long,
            ['disabled']: this.disabled,
            ['loading']: this.loading,
            ['icon-only']: !this.showSlot && (!!this.icon || this.loading)
          }
        ];
      },
      showSlot () {
        return !!Util.getLen(this.$slots);
      }
    },
    methods: {
      onclick() {
        this.$emit('click');
      }
    }
  }
</script>


