<template>
  <div ref="node">
    <slot></slot>
  </div>
</template>
<script type="text/javascript">
import animation from './openAnimation';
import * as Util from "../../utils";
const prefix = 'v-animate';
const oTransitionName = {
  'fade': {
    enter: 'fadeIn',
    leave: 'fadeOut'
  },
  'slide': {
    enter: 'slideDown',
    leave: 'slideUp'
  }
};
export default{
  name: prefix,
  props: {
    show: {
      type: Boolean,
      coerce: Util.coerceBoolean,
      default: false
    },
    duration: {
      type: Number,
      coerce: Util.coerceNumber,
      default: 400
    },
    transitionName: {
      type: String,
      default: 'fade'
    },
    onEnter: {
      type: Function,
      default () {
        return new Function();
      }
    },
    onLeave: {
      type: Function,
      default () {
        return new Function();
      }
    }
  },
  methods: {
    enter () {
      animation.enter(this.currentNode, this.duration, oTransitionName[this.transitionName].enter, () => {
        this.onEnter()
      });
    },
    leave () {
      animation.leave(this.currentNode, this.duration, oTransitionName[this.transitionName].leave, () => {
        this.onLeave();
      });
    }
  },
  watch: {
    show (val) {
      if(val) this.enter();
      else this.leave(); 
    }
  },
  computed: {
    currentNode () {
      return this.$refs.node;
    }
  },
  mounted () {
    if (!this.show) {
      this.currentNode.style.display = 'none';
    } else {
      this.currentNode.style.display = 'block';
    }
  }
}
</script>


