<!--

weiluxia 2016.07.22
name 
action 
multiple 
data 
accept 
maxSize 
fileList 
listType  text or image
onChange (uploading done error removed)
beforeUpload ,false,
-->
<template>
  <div class="v-upload">
    <div class="v-upload-select" @click.stop="handleClick">
      <input ref="file"
        type="file"
        :disabled="disabled"
        style="display: none"
        :accept="computed_accept"
        :multiple="multiple"
        @change="handleChange" />
      <v-button type="info" :disabled="disabled"><v-icon type="upload" /></v-button>
      <span class="v-upload-hint"><slot></slot></span>
    </div>
    <upload-list :list="computed_fileList" @del="onRemove" :list-type="listType"></upload-list>
  </div>
</template>
<script>
  import uploadList from "./UploadList.vue";
  import uploader from './uploader';
  const prefix = 'v-upload';
  export default {
    name: prefix,
    components: { uploadList },
    mixins: [ uploader ],
    props: {
      listType: {
        type: String,
        default: 'image'
      }
    }
  }
</script>


