<template>
  <span :class="wrapCls">
    <template v-if="type !== 'textarea'" >
      <!--  -->
      <span v-if="$slots.prepend" :class="withButton ? 'input-group-btn':'input-group-addon'">
        <slot name="prepend"></slot>
      </span>
      <input class="form-control"
        :type="type"
        ref="input"
        :name="name"
        :placeholder="placeholder"
        :disabled="disabled"
        :readonly="readonly"
        :maxlength="maxlength"
        :minlength="minlength"
        :autofocus="autofocus"
        :autocomplete="autoComplete"
        :value="value"
        @keydown.enter="keydownHandle"
        @focus="focusHandle"
        @blur="blurHandle"
        @input="inputHandle"
      />
      <v-icon v-if="withIcon" :type="icon"></v-icon>
      <span v-if="$slots.append" :class="withButton ? 'input-group-btn':'input-group-addon'">
        <slot name="append"></slot>
      </span>
    </template>
    <textarea v-else class="form-control"
      ref="textarea"
      :name="name"
      :placeholder="placeholder"
      :disabled="disabled"
      :readonly="readonly"
      :rows="rows"
      :autofocus="autofocus"
      :maxlength="maxlength"
      :minlength="minlength"
      v-model.trim="currentValue"
      @focus="focusHandle"
      @blur="blurHandle"
      @keydown.enter="keydownHandle"
    ></textarea>
  </span>
</template>
<script>
  const prefix = 'v-input';
  import vIcon from '../icon';
  import emitter from '../base/mixins/emitter';
  export default{
    name: prefix,
    mixins: [emitter],
    components: { vIcon },
    props: {
      value: [String, Number],
      placeholder: {
        type: String,
        default: ''
      },
      size: String,
      readonly: Boolean,
      autofocus: Boolean,
      icon: String,
      disabled: Boolean,
      withButton: Boolean,
      type: {
        type: String,
        default: 'text'
      },
      name: String,
      autosize: {
        type: [Boolean, Object],
        default: false
      },
      rows: {
        type: [String, Number],
        default: 2
      },
      autoComplete: {
        type: String,
        default: 'off'
      },
      icon: String,
      maxlength: Number,
      minlength: Number,
    },
    data () {
      return {
        currentValue: this.value,
      };
    },
    computed: {
      withIcon () {
        return !!this.icon && '' != this.icon;
      },
      wrapCls () {
        return [
          `${prefix}`,
          {
            ['input-group']: this.$slots.prepend || this.$slots.append,
            [`${prefix}-with-icon`]: this.withIcon
          }
        ];
      }
    },
    methods: {
      inputHandle () {
        this.currentValue = event.target.value;
      },
      blurHandle() {
        this.$emit('blur', event);
        this.dispatch('v-form-item', 'formItem.blur', [this.currentValue]);
      },
      focusHandle () {
        this.$emit('focus', event);
        this.dispatch('v-form-item', 'formItem.focus', [this.currentValue]);
      },
      keydownHandle () {
        this.$emit('enter', event);
      }
    },
    watch: {
      value (val) {
        this.currentValue = val;
      },
      currentValue (val) {
        this.$emit('input', val);
        this.$emit('change', val);
        this.dispatch('v-form-item', 'formItem.change', val);
      }
    }
  }
</script>


