<template>
  <label :class="wrapperCls">
    <span :class="inputCls">
      <input
        v-if="trueLabel || falseLabel"
        type="checkbox"
        :class="originalCls"
        :name="name"
        :disabled="disabled"
        v-bind:true-value="trueLabel"
        v-bind:false-value="falseLabel"
        v-model="_value" />
      <input
        v-else
        type="checkbox"
        :class="originalCls"
        :value="label"
        v-model="_value"
        :name="name"
        :disabled="disabled" />
      <span :class="innerCls"></span>
    </span>
    <span v-if="'' != text || $slots.default" :class="labelCls">
      <template v-if="!$slots.default">{{text}}</template>
      <slot></slot>
    </span>
  </label>
</template>
<script>
  const prefix = 'v-checkbox';
  import * as Util from "../../utils";
  import emitter from '../base/mixins/emitter';
  export default {
    name: prefix,
    props: {
      value: {
        type: [String, Number],
        default: ''
      },
      label: {
        type: [String, Number],
        required: true
      },
      disabled: Boolean,
      indeterminate: Boolean,
      name: String,
      text: {
        type: String,
        default: ''
      },
      trueLabel: [String, Number],
      falseLabel: [String, Number]
    },
    mixins: [emitter],
    data() {
      return {
        store: [],
        isGroup: false,
      };
    },
    computed: {
      _value: {
        get () {
          return this.isGroup ? this.store : this.value;
        }, 
        set (val) {
          if (this.isGroup) {
            this.dispatch('v-checkbox-group', 'input', [val]);
          } else {
            this.value = val;
            this.$emit('input', val);
            this.$emit('change', val);
          }
        }
      },
      isChecked() {
        const val = this._value;
        if (Util.isBoolean(val)) {
          return val;
        } else if (Array.isArray(val)) {
          return val.indexOf(this.label) > -1;
        } else if (val !== null && val !== undefined) {
          return val === this.trueLabel;
        }
      },
      wrapperCls () {
        return [
          `${prefix}`,
          {
            [`${prefix}-checked`]: this.isChecked,
            [`${prefix}-disabled`]: this.disabled,
            [`${prefix}-indeterminate`]: this.indeterminate,
          }
        ]
      },
      inputCls () {
        return `${prefix}-input`;
      },
      originalCls () {
        return `${prefix}-original`;
      },
      innerCls () {
        return `${prefix}-inner`;
      },
      labelCls () {
        return `${prefix}-label`;
      },
    },
    methods: {
      addToStore() {
        if (Array.isArray(this._value)) {
          this._value.indexOf(this.label) === -1 && this._value.push(this.label);
        } else {
          this._value = this.trueLabel || true;
        }
      }
    },
    created() {
      this.$on('initData', data => {
        this.store = data;
        this.isGroup = true;
      });
      this.$on('initDisabled', data => {
        this.disabled = data;
      });
    },
  }
</script>


