<template>
  <li :class="classes">{{ data.label }}<v-icon v-if="data.children && data.children.length" type="angle-right" :class="arrowCls"></v-icon></li>
</template>
<script>
  import vIcon from '../icon';
  const prefix = 'v-casitem';
  export default {
    name: prefix,
    props: {
      data: Object,
      prefixCls: String,
      tmpItem: Object
    },
    components: { vIcon },
    computed: {
      classes () {
        return [
          `${this.prefixCls}-menu-item`,
          {
            [`${this.prefixCls}-menu-item-noSelected`]: this.data.noSelected,
            [`${this.prefixCls}-menu-item-disabled`]: this.data.disabled,
            [`${this.prefixCls}-menu-item-active`]: this.tmpItem.value === this.data.value,
          }
        ];
      },
      arrowCls () {
        return `${this.prefixCls}-menu-item-arrow`;
      }
    }
  };
</script>



