<template>
  <v-dialog
    v-model="store"
    :prefix-cls="wrapCls"
    :width="width"
    :type="type"
    :show="show"
    :closable="closable"
    :mask-closable="maskClosable"
    :footer-hide="footerHide"
    :ok-btn="okBtn"
    :cancel-btn="cancelBtn"
    :loading="loading"
    :confirm-loading="confirmLoading"
    :title="title"
    :ok-text="okText"
    :cancel-text="cancelText"
    :style="style"
    :wrap-class-name="wrapClassName"
    @open="open"
    @close="close"
    @save="onSave"
    @cancel="onCancel"
  >
    <slot></slot>
  </v-dialog>
</template>
<script>
  import * as Util from "../../utils";
  import vDialog from '../base/dialog';
  const prefix = 'v-modal';
  export default{
    name: prefix,
    components: { vDialog },
    props: {
      value: Boolean,
      width: {
        type: Number,
        default: 520
      },
      type: {
        type: String,
        default: 'info'
      },
      closable: {
        type: Boolean,
        default: true
      },
      maskClosable: {
        type: Boolean,
        default: true
      },
      show: {
        type: Boolean,
        default: false
      },
      footerHide: {
        type: Boolean,
        default: false
      },
      okBtn: {
        type: Boolean,
        default: true
      },
      cancelBtn: {
        type: Boolean,
        default: true
      },
      loading: {
        type: Boolean,
        default: false
      },
      confirmLoading: {
        type: Boolean,
        default: false
      },
      title: {
        type: String,
        default: ''
      },
      cancelText: {
        type: String,
        default: ''
      },
      okText: {
        type: String,
        default: ''
      },
      style: {
        type: Object,
        default () {
          return {};
        }
      },
      wrapClassName: {
        type: String,
        default: ''
      },
    },
    data () {
      return {
        wrapCls: `modal ${prefix}`,
        store: this.value
      }
    },
    watch: {
      store(val) {
        this.$emit('input', val);
      },
      value(val) {
        this.store = val;
      }
    },
    methods: {
      onSave () {
        this.$emit('save');
      },
      onCancel () {
        this.$emit('cancel');
      },
      open () {
        this.$emit('open');
      },
      close () {
        this.$emit('close');
      }
    }
  }
</script>


