<template>
  <div :class="wrapCls" style="padding-left: 0;padding-right: 0">
    <slot></slot>
  </div>
</template>
<script>
  export default{
    name: 'v-row',
    data () {
      return {
        wrapCls: 'col-sm-12'
      };
    }
  }
</script>


