<template>
  <div class="v-tree">
    <v-tree-item v-for="child in tree.root.children" :node="child"></v-tree-item>
  </div>
</template>

<script>
  import Tree from './model/tree';
  import vTreeItem from './TreeItem.vue';
  import * as Util from '../../utils'; 
  export default {
    name: 'v-tree',
    components: { vTreeItem },
    props: {
      data: {
        type: Array
      },
      showCheckbox: {
        type: Boolean,
        default: false
      },
      config: {
        default() {
          return {
            children: 'children',
            label: 'label',
            icon: 'icon',
            highlight: 'highlight'
          };
        }
      },
      lazy: {
        type: Boolean,
        default: false
      },
      loadData: {
        type: Function,
        default () {
          return new Function();
        }
      },
      onExpand: {
        type: Function,
        default () {
          return new Function();
        }
      },
      onCheck: {
        type: Function,
        default () {
          return new Function();
        }
      },
      onSelect: {
        type: Function,
        default () {
          return new Function();
        }
      }
    },
    created() {
      let self = this;
      self.$isTree = true;
      self.tree = new Tree({
        data: self.data,
        lazy: self.lazy,
        props: self.config,
        load: self.loadData
      });
      self.$on('node-click',function(node){
        self.selectItem = node;
      });
    },
    data() {
      return {
        tree: {},
        selectItem:{}
      };
    },
    computed: {
      children: {
        set(value) {
          this.data = value;
        },
        get() {
          return this.data;
        }
      }
    },
    watch: {
      data (val) {
        this.tree.root.setData(val);
      },
      selectItem(){
        this.onSelect(this.selectItem);
      }
    },
    methods: {
      getCheckedNodes(leafOnly) {
        return this.tree.getCheckedNodes(leafOnly);
      }
    }
  };
</script>



