<template>
  <div :class="wrapCls" v-clickoutside="handleClose">
    <div :class="inputCls" @click="handleToggleOpen">
      <v-input
        readonly
        :disabled="disabled"
        v-model="displayRender"
        :placeholder="placeholder"
      >
      </v-input>
      <v-icon type="times-circle" :class="delCls" v-show="showCloseIcon" @click.native.stop="handleClearSelect"></v-icon>
      <div :class="arrowCls"></div>
    </div>
    <transition name="slide-up">
      <div :class="dropdownCls" v-show="visible" @mouseleave.stop="handleLeave">
        <v-caspanel
          ref="caspanel"
          :prefix-cls="prefixCls"
          :data="data"
          :disabled="disabled"
          :change-on-select="changeOnSelect"
          :trigger="trigger"
          @on-result-change="onResultChange">
        </v-caspanel>
      </div>
    </transition>
  </div>
</template>
<script>
    import vInput from '../input';
    import vIcon from '../icon';
    import vCaspanel from './Caspanel.vue';
    import clickoutside from '../base/directives/clickoutside';
    import emitter from '../base/mixins/emitter';
    const prefix = 'v-cascader';
    
    export default {
      name: prefix,
      components: { vInput, vIcon, vCaspanel },
      directives: { clickoutside },
      mixins: [emitter],
      props: {
        value: {
          type: Array,
          default () {
            return [];
          }
        },
        data: {
          type: Array,
          default () {
            return [];
          }
        },
        disabled: {
          type: Boolean,
          default: false
        },
        clearable: {
          type: Boolean,
          default: true
        },// 
        placeholder: {
          type: String,
          default: ''
        },
        trigger: {
          type: String,
          default: 'click'
        },
        changeOnSelect: {
          type: Boolean,
          default: false
        },
        renderFormat: {
          type: Function,
          default (label) {
            return label.join(' / ');
          }
        }
      },
      data () {
        return {
          inputCls: `${prefix}-input`,
          dropdownCls: `${prefix}-dropdown`,
          visible: false,
          prefixCls: prefix,
          computed_value: this.value,
          tmpComputed_value: [],
          updatingValue: false
        };
      },
      watch: {
        value (newVal, oldVal) {
          if((0 == newVal.length) && (0 == oldVal.length)) return;
          this.computed_value = newVal;
          this.updateSelected();
          this.dispatch('v-form-item', 'formItem.change', [newVal]);
          
        },
        computed_value (val) {
          this.$emit('input', val);
          this.$emit('change', val);
        }
      },
      computed: {
        wrapCls () {
          return [
            prefix,
            {
              [`${prefix}-disabled`]: this.disabled
            }
          ]
        },
        delCls () {
          return `${this.inputCls}-del`;
        },
        arrowCls () {
          return [
            `${this.inputCls}-arrow`,
            {
              [`${this.inputCls}-arrow-expand`]: this.visible
            }
          ]
        },
        showCloseIcon () {
          return this.computed_value && this.computed_value.length && this.clearable && !this.disabled;
        },
        displayRender () {
          let label = [];
          for (let i = 0; i < this.computed_value.length; i++) {
            label.push(this.computed_value[i].label);
          }
          return this.renderFormat(label, this.computed_value);
        }
      },
      methods: {
        handleClose () {
          this.visible = false;
        },
        handleToggleOpen () {
          if(this.disabled) return;
          if (this.visible) {
            this.handleClose();
          } else {
            this.onFocus();
          }
        },
        onFocus () {
          this.visible = true;
          if (!this.computed_value.length) {
            this.$refs.caspanel.clearSelect();
          }
        },
        handleClearSelect () {
          this.computed_value = this.tmpComputed_value = [];
          this.handleClose();
          this.$refs.caspanel.clearSelect();
        },
        handleLeave () {
          if(this.trigger == 'hover') this.handleClose();
        },
        updateResult (result) {
          this.tmpComputed_value = result;
        },
        onResultChange ({result, lastValue, changeOnSelect, fromInit}) {
          if (lastValue || changeOnSelect) {
            // 
            const oldVal = JSON.stringify(this.computed_value);
            this.computed_value = this.tmpComputed_value;

            let newVal = [];
            this.computed_value.forEach((item) => {
              newVal.push(item.value);
            });

            if (!fromInit) {
              this.updatingValue = true;
            }
          }
          if (lastValue && !fromInit) {
            this.handleClose();
          }
        },
        updateSelected () {
          this.$refs.caspanel.initSelect(this.computed_value);
        }
      },
      mounted () {
        this.updateSelected();
      }
    };
</script>



