<template>
<i class="fa" :class="iconClasses" @click="onclick">
  <slot></slot>
</i>
</template>

<script>
import * as Util from "../../utils";
const prefix = 'v-icon';
export default {
  name: 'icon',
  props: {
    className: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    }
  },
  computed: {
    iconClasses () {
      return [
        this.className,
        'fa-' + this.type
      ];
    }
  },
  methods: {
    onclick() {
      this.$emit('click');
    }
  },
}
</script>



