export default {
  methods: {
    bindTriggerEvent () {
      const trigger = this.$slots.trigger[0].elm;
      if (trigger) {
        trigger.addEventListener('blur', this.onBlur.bind(this), false);
        trigger.addEventListener('click', this.onClick.bind(this), false);
        trigger.addEventListener('focus', this.onFocus.bind(this), false);
        trigger.addEventListener('mouseenter', this.onMouseEnter.bind(this), false);
        trigger.addEventListener('mouseleave', this.onMouseLeave.bind(this), false);
      }
    },
    offTriggerEvent () {
      const trigger = this.$slots.trigger[0].elm;
      if (trigger) {
        trigger.removeEventListener('blur', this.onBlur.bind(this), false);
        trigger.removeEventListener('click', this.onClick.bind(this), false);
        trigger.removeEventListener('focus', this.onFocus.bind(this), false);
        trigger.removeEventListener('mouseenter', this.onMouseEnter.bind(this), false);
        trigger.removeEventListener('mouseleave', this.onMouseLeave.bind(this), false);
      }
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.bindTriggerEvent();
    });
  },
  beforeDestroy () {
    this.offTriggerEvent();
  }
}



