<!--
  
  title 
  placement 
  okText 
  cancelText  
  onCancel 
  onConfirm 
-->
<template>
  <span>
    <slot name="trigger"></slot>
    <!-- <transition :name="transition"> -->
      <div ref="popup" :class="popupCls" v-show="computed_value" @click.stop>
        <div class="v-popover-arrow"></div>
        <div class="v-popover-inner">
          <div class="v-popover-inner-desc">
            <div class="v-popover-message" v-html="title"></div>
            <div class="v-popover-buttons">
              <v-button size="xSmall" type="ghost" @click="cancelHandle">{{ cancelText }}</v-button>
              <v-button size="xSmall" type="info" @click="confirmHandle">{{ okText }}</v-button>
            </div>
          </div>
        </div>
      </div>
    <!-- </transition> -->
  </span>
</template>
<script>
  import trigger from "../base/trigger/trigger";
  import vButton from "../button";
  const prefix = 'v-popconfirm';
  export default{
    name: prefix,
    components: { vButton },
    mixins: [ trigger ],
    props: {
      title: {
        type: String,
        default: ''
      },
      content: {
        type: String,
        default: ''
      },
      trigger: {
        type: String,
        default: 'click'
      },
      transition: {
        type: String,
        default: 'ease'
      },
      cancelText: {
        type: String,
        default: ''
      },
      okText: {
        type: String,
        default: ''
      },
    },
    data () {
      return {
        prefixCls: 'v-popover'
      }
    },
    methods: {
      cancelHandle () {
        this.computed_value = false;
        this.$emit('cancel');
      },
      confirmHandle () {
        this.computed_value = false;
        this.$emit('confirm');
      }
    }
  }
</script>


