import * as Util from "../../utils";
import vButton from "../button";
export default{
  components: { vButton },
  props: {
    columns: {
      type: Array,
      default () {
        return [];
      }
    },
    dataSource: {
      type: Array,
      default () {
        return [];
      }
    }
  },
  data () {
    return {
    };
  },
  computed: {
  },
  watch: {
  },
  methods: {
  },
  render (h) {
    return (
      <table 
        class="table table-hover"
        cellspacing="0"
        cellpadding="0"
        border="0">
        {
          this._l(this.columns, column =>
            <col
              name={ column.id }
              width={ column.realWidth || column.width }/>
          )
        }
        <thead>
          <tr>
            {
              this._l(this.columns, (column, rowIndex) =>
                <th><span>{column.title}</span></th>
              )
            }
          </tr>
        </thead>
        <tbody>
          {
            this._l(this.dataSource, row =>
              <tr>
                {
                  this._l(this.columns, (column, rowIndex) =>
                    <td>
                      {
                        column.render ? column.render.call(this._renderProxy, h, {row, column, $index: rowIndex}) : row[column.key]
                      }
                    </td>
                  )
                }
              </tr>
            )
          }
        </tbody>
      </table>
    );
  }
};



